import React, { useCallback, useEffect, useMemo, useState } from 'react';

import { LinkDefault } from '../../../icons';
import type { PuiLinkBasicProps } from '../types';
import { LinkFieldDisplayTypes } from '../types';
import { getFaviconUrl, validateUrl } from './helpers';
import { LinkFaviconComponent } from './LinkFaviconComponent';
import { StyledLink, StyledLinkWrap } from './StyledComponents';

export const LinkItemComponent: React.FC<PuiLinkBasicProps> = ({ link, displayType, isActive, displayDefaultIcon }) => {
  const [faviconUrl, setFaviconUrl] = useState<string | null>(null);

  useEffect(() => {
    if (!validateUrl(link.href)) {
      return;
    }

    const fetchFavicon = async () => {
      const url = await getFaviconUrl(link.href);
      setFaviconUrl(url);
    };

    fetchFavicon();
  }, [link]);

  const onLinkClick = useCallback((event: React.MouseEvent<HTMLAnchorElement, MouseEvent>): void => {
    event.stopPropagation();
  }, []);

  const componentsMap = useMemo(
    () => ({
      [LinkFieldDisplayTypes.LinkOnly]: (
        <StyledLink
          href={link.href}
          target='_blank'
          onClick={onLinkClick}
        >
          {link.value}
        </StyledLink>
      ),
      [LinkFieldDisplayTypes.LinkWithIcon]: (
        <StyledLink
          href={link.href}
          underline='hover'
          target='_blank'
          onClick={onLinkClick}
        >
          {displayDefaultIcon ? (
            <LinkDefault
              width={14}
              height={14}
            />
          ) : (
            <LinkFaviconComponent faviconUrl={faviconUrl} />
          )}
          <span>{link.value}</span>
        </StyledLink>
      ),
      [LinkFieldDisplayTypes.IconOnly]: (
        <StyledLink
          className='icon-wrap'
          onClick={onLinkClick}
          href={link.href}
          underline='hover'
          target='_blank'
        >
          {displayDefaultIcon ? (
            <LinkDefault
              width={14}
              height={14}
            />
          ) : (
            <LinkFaviconComponent faviconUrl={faviconUrl} />
          )}
        </StyledLink>
      ),
    }),
    [link, faviconUrl, onLinkClick, displayDefaultIcon]
  );

  return (
    <StyledLinkWrap className={`${isActive ? 'active' : 'inactive'} ${displayType}`}>
      {link.href && componentsMap[displayType]}
    </StyledLinkWrap>
  );
};
