import { LinkItemComponent } from '../common/LinkItemComponent';
import type { PuiLinkBasicProps } from '../types';
import { ViewLinkWrapper } from './StyledComponents';

export const PuiLinkView: React.FC<PuiLinkBasicProps> = ({
  link,
  displayType,
  isActive = false,
  displayDefaultIcon,
  sx,
}) => {
  return link ? (
    <ViewLinkWrapper sx={sx}>
      <LinkItemComponent
        link={link}
        displayType={displayType}
        isActive={isActive}
        displayDefaultIcon={displayDefaultIcon}
      />
    </ViewLinkWrapper>
  ) : (
    <></>
  );
};
