import { useTheme } from '../../../hooks';
import type { SliderDisplayType } from '../types';

export interface SliderDisplayParams {
  sliderLineHeight: string;
  railBackgroundColor: string;
  border: string;
}

export const useSliderParams = (displayType: SliderDisplayType, mainColor?: string) => {
  const theme = useTheme();
  const color = mainColor ?? theme.palette.success.main;

  const sliderParamsByDisplayType: Record<SliderDisplayType, SliderDisplayParams> = {
    small: {
      sliderLineHeight: '4px',
      railBackgroundColor: theme.palette.grey[50],
      border: 'none',
    },
    medium: {
      sliderLineHeight: '8px',
      railBackgroundColor: theme.palette.grey[50],
      border: 'none',
    },
    big: {
      sliderLineHeight: '10px',
      railBackgroundColor: theme.palette.grey[50],
      border: 'none',
    },
  };

  return {
    sliderParams: sliderParamsByDisplayType[displayType],
    color,
  };
};
