import type { FabProps } from '@mui/material';
import Fab from '@mui/material/Fab';
import type { ReactNode } from 'react';

import { useTheme } from '../../hooks';
import { PuiStyled } from '../Styled';

interface PuiFabProps extends FabProps {
  background?: string;
  boxShadow?: string;
  children: ReactNode;
}

export const PuiFab = (props: PuiFabProps) => {
  const theme = useTheme();
  const { background, boxShadow, children, ...fabProps } = props;

  const StyledFab = PuiStyled(Fab)({
    background: background || theme.palette.primary.main,
    boxShadow: boxShadow || '0px -4px 12px 0px rgba(0, 0, 0, 0.05), -4px 8px 16px -2px rgba(27, 33, 44, 0.12)',
    '&:hover': {
      background: background || theme.palette.primary.main,
    },
    '&:active': {
      boxShadow: boxShadow || '0px -4px 12px 0px rgba(0, 0, 0, 0.05), -4px 8px 16px -2px rgba(27, 33, 44, 0.12)',
    },
  });

  return <StyledFab {...fabProps}>{children}</StyledFab>;
};
