import { PuiIcon, PuiSvgIcon } from '../../../icons';

interface LinkFaviconComponentProps {
  faviconUrl: string | null;
}

export const LinkFaviconComponent: React.FC<LinkFaviconComponentProps> = ({ faviconUrl }) => {
  return faviconUrl ? (
    <img
      src={faviconUrl}
      alt='Favicon'
      style={{ width: 13, height: 13 }}
    />
  ) : (
    <PuiSvgIcon
      icon={PuiIcon.Globe}
      width='13px'
      height='13px'
    />
  );
};
