import { useTheme } from '@mui/material';

import { PuiIcon, PuiSvgIcon } from '../../icons';
import type { PuiUser } from '../../types';
import {
  StyledListItem,
  StyledPersonOptionContent,
  StyledPersonsAvatar,
  StyledPersonTypography,
} from './StyledComponents';

interface PuiPersonsListItemProps {
  user: PuiUser;
  id: string;
  isSelected: boolean;
  isHighlighted: boolean;
  onClick: () => void;
}

export const PuiPersonsListItem = ({ user, isSelected, onClick, isHighlighted, id }: PuiPersonsListItemProps) => {
  const theme = useTheme();

  return (
    <StyledListItem
      id={id}
      key={user.id}
      onClick={onClick}
      style={isHighlighted ? { backgroundColor: theme.palette.action.hover } : {}}
    >
      <StyledPersonsAvatar
        size={28}
        contact={user}
        className={isSelected ? 'avatar-active' : ''}
      />
      <StyledPersonOptionContent>
        <StyledPersonTypography noWrap>{user.email}</StyledPersonTypography>
        <PuiSvgIcon
          icon={PuiIcon.Check}
          width={20}
          height={20}
          stroke={theme.palette.primary.main}
          style={{ opacity: isSelected ? '1' : '0' }}
        />
      </StyledPersonOptionContent>
    </StyledListItem>
  );
};
