import type { ChangeEvent, KeyboardEvent } from 'react';
import { useState } from 'react';

import { isLengthTooBig, makeCaretFocusHandler, processNumberForSave } from '../../utils';
import { currencies } from '../currencies';
import { StartAdornmentCurrency, TextInput, TextInputContainer } from '../StyledComponents';
import type { PuiCurrencyEditProps } from '../types';

export const PuiCurrencyEdit: React.FC<PuiCurrencyEditProps> = ({
  value,
  disabled,
  signed,
  defaultCurrency,
  isLoading,
  onSave,
  onChange,
  inputRef,
  autoFocus = false,
  placeholder = 'Enter amount',
}) => {
  const currencySymbolWithCode = defaultCurrency ? `${currencies[defaultCurrency].symbol} - ${defaultCurrency}` : '';
  const [number, setNumber] = useState<string>(value);
  const decimalPlaces = 2;
  const handleFocus = makeCaretFocusHandler();
  const hasOnChange: boolean = typeof onChange === 'function';

  const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>): void => {
    const regEx = signed ? /^-?\d+(\.\d+)?$|^-?\d+\.$/ : /^\d+(\.\d+)?$|^\d+\.$/;
    const extraCheck = signed ? '-' : '';
    let curValue: string = e.currentTarget.value;

    if (curValue.indexOf(',') !== -1) {
      curValue = curValue.replace(',', '.');
    }
    if (isLoading || isLengthTooBig(curValue)) return;
    if (curValue && curValue !== extraCheck && !regEx.test(curValue)) return;

    setNumber(curValue);
    hasOnChange && onChange(curValue);
  };

  const onKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
    if (!e.shiftKey && e.key === 'Enter') {
      onSave(processNumberForSave(number, decimalPlaces));

      return;
    }

    const countToChange = e.shiftKey ? 10 : 1;

    if (e.key === 'Down' || e.key === 'ArrowDown') {
      setNumber(prevVal => {
        const newVal = prevVal ? String(+prevVal - countToChange) : '1';
        hasOnChange && onChange(newVal);

        return newVal;
      });

      return;
    }
    if (e.key === 'Up' || e.key === 'ArrowUp') {
      setNumber(prevVal => {
        const newVal = prevVal ? String(+prevVal + countToChange) : '0';
        hasOnChange && onChange(newVal);

        return newVal;
      });
    }
  };

  return (
    <TextInputContainer>
      <TextInput
        size='small'
        onChange={handleChange}
        value={number}
        onKeyDown={onKeyDown}
        multiline
        maxRows={12}
        disabled={disabled}
        autoFocus={autoFocus}
        placeholder={placeholder}
        onFocus={handleFocus}
        startAdornment={
          <StartAdornmentCurrency
            variant='body-sm-regular'
            onMouseDown={e => e.preventDefault()}
          >
            {currencySymbolWithCode}
          </StartAdornmentCurrency>
        }
        inputRef={inputRef}
      />
    </TextInputContainer>
  );
};
