import type { BoxProps } from '@mui/material';

import { PuiBox, PuiTypography } from '..';
import { ListItem, ListItemLink } from './StyledComponents';
import { InfoItemType } from './types';

interface PuiContactInfoItemProps {
  title: string;
  text: string;
  icon: React.ReactNode;
  type?: InfoItemType;
  sx?: BoxProps['sx'];
}

export const PuiContactInfoItem: React.FC<PuiContactInfoItemProps> = ({ title, text, icon, type, sx }) => {
  const isLink = type === InfoItemType.Email || type === InfoItemType.Phone;
  const href = type === InfoItemType.Email ? `mailto:${text}` : `tel:${text}`;
  const Component = isLink ? ListItemLink : ListItem;

  return (
    <Component
      sx={sx}
      {...(isLink && { href })}
    >
      {icon}
      <PuiBox>
        <PuiTypography
          variant='body-xs-regular'
          sx={{ textTransform: 'capitalize' }}
        >
          {title}
        </PuiTypography>
        <PuiTypography variant='body-sm-medium'>{text}</PuiTypography>
      </PuiBox>
    </Component>
  );
};
