import type { BoxProps, TypographyProps } from '@mui/material';

import { RestEmailsCount, TextViewTypography, TextViewWrapper } from './StyledComponents';

interface PuiEmailViewProps {
  value: string;
  restItemsCount: number;
  wrapperSx?: BoxProps['sx'];
  restCountSx?: TypographyProps['sx'];
  onResetClick?: () => void;
}

export const PuiEmailView: React.FC<PuiEmailViewProps> = ({ value, restItemsCount, wrapperSx, restCountSx }) => {
  return (
    <TextViewWrapper sx={wrapperSx}>
      <TextViewTypography
        variant='body-sm-regular'
        style={{ paddingRight: restItemsCount ? '44px' : undefined }}
      >
        {value}
      </TextViewTypography>
      {restItemsCount ? <RestEmailsCount sx={restCountSx}>+{restItemsCount}</RestEmailsCount> : null}
    </TextViewWrapper>
  );
};
