import React, { useMemo } from 'react';

import { StyledDropdownMenuView } from '../edit/StyledComponents';
import { getSingleSelectOptionComponent } from '../getSingleSelectOptionComponent';
import type { SingleSelectDisplayTypes, SingleSelectOption } from '../types';
import { OnHoverSelectActions } from './OnHoverSelectActions';
import { SelectValueWrapper, StyledSingleSelectView } from './StyledComponents';

interface PuiSingleSelectViewProps {
  singleSelectOptions: SingleSelectOption[];
  displayType?: SingleSelectDisplayTypes;
  selectedValue?: string;
  onHoverViewActions?: boolean;
  onClearValue?: () => void;
  manualCellEdit?: () => void;
}

export const PuiSingleSelectView: React.FC<PuiSingleSelectViewProps> = ({
  singleSelectOptions,
  displayType,
  selectedValue,
  onClearValue,
  manualCellEdit,
  onHoverViewActions,
}) => {
  const OptionItem = getSingleSelectOptionComponent(displayType);

  const selectedOption = useMemo(() => {
    if (!selectedValue || !singleSelectOptions) {
      return null;
    }
    const selectedIndex = singleSelectOptions.findIndex(
      option => option.id !== undefined && option.id === selectedValue
    );

    return singleSelectOptions[selectedIndex];
  }, [selectedValue, singleSelectOptions]);

  return (
    <StyledSingleSelectView>
      {selectedOption ? (
        <StyledDropdownMenuView
          key={selectedOption.id}
          className='single-select-view-item'
        >
          <SelectValueWrapper>
            <OptionItem
              option={selectedOption}
              viewMode={true}
            />
          </SelectValueWrapper>
          {onHoverViewActions && (
            <OnHoverSelectActions
              onClearValue={onClearValue}
              manualCellEdit={manualCellEdit}
              selectedValue={!!selectedOption}
            />
          )}
        </StyledDropdownMenuView>
      ) : (
        <>
          {onHoverViewActions && (
            <OnHoverSelectActions
              onClearValue={onClearValue}
              manualCellEdit={manualCellEdit}
              selectedValue={!!selectedOption}
            />
          )}
        </>
      )}
    </StyledSingleSelectView>
  );
};
