import type { InputProps, MenuProps } from '@mui/material';
import { useEffect, useState } from 'react';

import { PuiFade } from '../../../';
import type { PhoneValueType } from '../types';
import { validatePhone } from '../validatePhone';
import { PhoneInput } from './PhoneInput';
import { PopupButton, PopupButtonsWrapper, PopupMenu } from './StyledComponents';

interface PhonePopupMenuProps {
  initialValue: string;
  initialCountryCode: string;
  isOpened: boolean;
  isEditing?: boolean;
  anchorEl: HTMLElement | null;
  inputSx?: InputProps['sx'];
  popupSx?: MenuProps['sx'];
  onCloseMenu: () => void;
  onSave: (value: PhoneValueType) => void;
}

export const PhonePopupMenu: React.FC<PhonePopupMenuProps> = ({
  initialValue,
  initialCountryCode,
  isOpened,
  isEditing,
  anchorEl,
  inputSx,
  popupSx,
  onCloseMenu,
  onSave,
}) => {
  const [value, setValue] = useState(initialValue);
  const [countryCode, setCountryCode] = useState(initialCountryCode);
  const [validationError, setValidationError] = useState<null | string>(null);

  const handleSave = () => {
    const isValid = validatePhone(value, countryCode);

    if (!isValid) {
      setValidationError('Enter a valid phone number');

      return;
    }
    if (initialValue === value) {
      onCloseMenu();

      return;
    }

    setValidationError(null);
    onSave({ phone: value, countryCode });
    onCloseMenu();
  };

  const handleCloseMenu = () => {
    setValue(initialValue);
    setCountryCode(initialCountryCode);
    setValidationError(null);
    onCloseMenu();
  };

  const handleChange = (value: string, newCountryCode: string) => {
    setValue(value);
    countryCode !== newCountryCode && setCountryCode(newCountryCode);
  };

  useEffect(() => {
    setValue(initialValue?.replace(' ', ''));
    setCountryCode(initialCountryCode);
  }, [initialValue, initialCountryCode]);

  return (
    <PopupMenu
      elevation={0}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'left',
      }}
      anchorEl={anchorEl}
      open={isOpened}
      sx={popupSx}
      onClose={handleCloseMenu}
      TransitionComponent={PuiFade}
      data-testid='phone-popup-menu'
      slotProps={{
        root: { sx: { pointerEvents: 'none' } },
        paper: { sx: { pointerEvents: 'auto' } },
      }}
    >
      {isEditing && !countryCode ? null : (
        <PhoneInput
          value={value}
          defaultCountry={countryCode}
          validationError={validationError}
          styleMod='bordered'
          sx={inputSx}
          onChange={handleChange}
        />
      )}
      <PopupButtonsWrapper>
        <PopupButton onClick={handleCloseMenu}>Cancel</PopupButton>
        <PopupButton
          variant='contained'
          onClick={handleSave}
        >
          Save
        </PopupButton>
      </PopupButtonsWrapper>
    </PopupMenu>
  );
};
