import { ComponentMode } from '../../types';
import { SingleSelectEdit } from './edit/SingleSelectEdit';
import type { SingleSelectDisplayTypes, SingleSelectOption } from './types';
import { PuiSingleSelectView } from './view/SingleSelectView';

interface PuiSingleSelectProps {
  value: string;
  displayType?: SingleSelectDisplayTypes;
  options: SingleSelectOption[];
  disabled?: boolean;
  onChange: (value: string) => void;
  mode?: ComponentMode;
  className?: string;
  onMenuOpen?: () => void;
  onMenuClose?: () => void;
  onClearValue?: () => void;
  manualCellEdit?: () => void;
  open?: boolean;
  placeholder?: string;
  onHoverViewActions?: boolean;
}

export const PuiSingleSelect: React.FC<PuiSingleSelectProps> = ({
  options = [],
  disabled,
  value,
  displayType,
  onChange,
  mode = ComponentMode.Edit,
  className,
  onMenuOpen,
  onMenuClose,
  open,
  placeholder,
  onHoverViewActions = false,
  onClearValue,
  manualCellEdit,
}) => {
  if (mode === ComponentMode.View) {
    return (
      <PuiSingleSelectView
        singleSelectOptions={options}
        selectedValue={value}
        displayType={displayType}
        onClearValue={onClearValue}
        manualCellEdit={manualCellEdit}
        onHoverViewActions={onHoverViewActions}
      />
    );
  }

  if (mode === ComponentMode.Edit) {
    return (
      <SingleSelectEdit
        disabled={disabled}
        onChange={onChange}
        options={options}
        value={value}
        displayType={displayType}
        className={className}
        onMenuOpen={onMenuOpen}
        onMenuClose={onMenuClose}
        open={open}
        placeholder={placeholder}
      />
    );
  }

  return null;
};
