import type { SelectChangeEvent } from '@mui/material/Select';
import { useEffect, useState } from 'react';

export interface UseDropdownProps<T> {
  open: boolean;
  options: T[];
  selectedValue?: string;
  disabled: boolean;
  onValueChange: (value: string) => void;
  onMenuClose?: () => void;
}

export const useDropdown = <T extends { id: string }>({
  open,
  options,
  selectedValue,
  onValueChange,
  disabled,
  onMenuClose,
}: UseDropdownProps<T>) => {
  const [selectedOptionIndex, setSelectedOptionIndex] = useState<number>(-1);

  useEffect(() => {
    if (selectedValue && options) {
      const selectedIndex = options.findIndex(option => option.id === selectedValue);
      setSelectedOptionIndex(selectedIndex);
    }
  }, [selectedValue, options]);

  const handleArrowNavigation = (event: KeyboardEvent) => {
    if (!open) {
      return;
    }
    if (event.key === 'Escape') {
      onMenuClose?.();

      return;
    }
    const navKeys = ['ArrowUp', 'ArrowDown'];
    if (navKeys.includes(event.key)) {
      const nextIndex = (selectedOptionIndex + (event.key === 'ArrowDown' ? 1 : -1) + options.length) % options.length;
      setSelectedOptionIndex(nextIndex);

      return;
    }
    if (event.key === 'Enter') {
      handleChange({ target: { value: selectedOptionIndex } } as SelectChangeEvent<unknown>);
      onMenuClose?.();
    }
  };

  const handleChange = (event: SelectChangeEvent<unknown>) => {
    if (disabled) {
      return;
    }

    if (typeof event.target.value === 'string') {
      const selectedValue = event.target.value;
      onValueChange(selectedValue);

      return;
    }

    if (typeof event.target.value === 'number') {
      const selectedIndex = event.target.value;
      const selectedId = options[selectedIndex] ? options[selectedIndex].id : '';
      onValueChange(selectedId);

      return;
    }

    onValueChange('');
  };

  const handleReset = (event: React.MouseEvent) => {
    event.stopPropagation();
    if (disabled) {
      return;
    }

    setSelectedOptionIndex(-1);
    onValueChange('');
    onMenuClose?.();
  };

  return {
    selectedOptionIndex,
    handleChange,
    handleReset,
    handleArrowNavigation,
  };
};
