import type { ChangeEvent } from 'react';
import React, { useState } from 'react';

import { Eye, EyeOff } from '..';
import { PuiIconButton } from './button';
import { PuiInputAdornment, PuiTextField } from './Form';

interface PasswordInputProps {
  size?: 'small' | 'medium';
  id: string;
  'aria-describedby'?: string;
  placeholder?: string;
  value: string;
  onChange: (e: ChangeEvent<HTMLInputElement>) => void;
  error?: boolean;
  required?: boolean;
  fullWidth?: boolean;
}

const PuiPasswordInput: React.FC<PasswordInputProps> = ({
  size = 'small',
  id,
  'aria-describedby': ariaDescribedBy,
  placeholder = '',
  value,
  onChange,
  error = false,
  required = false,
  fullWidth = false,
}) => {
  const [showPassword, setShowPassword] = useState(false);

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  return (
    <PuiTextField
      size={size}
      id={id}
      aria-describedby={ariaDescribedBy}
      placeholder={placeholder}
      type={showPassword ? 'text' : 'password'}
      value={value}
      onChange={onChange}
      required={required}
      error={error}
      InputProps={{
        endAdornment: (
          <PuiInputAdornment position='end'>
            <PuiIconButton
              aria-label='Toggle password visibility'
              onClick={handleClickShowPassword}
              edge='end'
            >
              {showPassword ? <Eye /> : <EyeOff />}
            </PuiIconButton>
          </PuiInputAdornment>
        ),
      }}
      fullWidth={fullWidth}
    />
  );
};

export { PuiPasswordInput };
