import type { ButtonProps } from '@mui/material';
import Button from '@mui/material/Button';
import React from 'react';

import { PuiCircularProgress } from '../index.ts';
import { getLoadingButtonStyles, getProgressSize } from './utils.ts';

declare module '@mui/material/Button' {
  interface ButtonPropsSizeOverrides {
    xsmall: true;
    xxsmall: true;
  }
}

interface ExtendedButtonProps extends ButtonProps {
  loading?: boolean;
}

const PuiLoadingButton: React.FC<ExtendedButtonProps> = ({ variant, loading, size, startIcon, children, ...props }) => {
  const extraStyle = getLoadingButtonStyles(loading, variant);

  return (
    <Button
      variant={variant}
      size={size}
      startIcon={!loading ? startIcon : null}
      style={extraStyle.button}
      {...props}
    >
      {loading && (
        <PuiCircularProgress
          style={extraStyle.progress}
          size={getProgressSize(size)}
        />
      )}
      {loading ? <span style={extraStyle.childrenWrap}>{children}</span> : <span>{children}</span>}
    </Button>
  );
};

export { PuiLoadingButton };
