import { useTheme } from '@mui/material';
import React from 'react';

import { ClassicStatusItem } from '../edit/ClassicStatusItem';
import { FillStatusItem } from '../edit/FillStatusItem';
import { LeftIndicatorStatusItem } from '../edit/LeftIndicatorStatusItem';
import { RoundStatusItem } from '../edit/RoundStatusItem';
import type { StatusViewProps } from '../types';
import { StatusDisplayTypes } from '../types';
import { OnHoverStatusActions } from './OnHoverStatusActions';
import {
  StatusValueWrapper,
  StyledStatusView,
  StyledViewClassicStatusItem,
  StyledViewFillStatusItem,
  StyledViewLeftIndicatorStatusItem,
  StyledViewRoundStatusItem,
} from './StyledComponents';

export const PuiStatusView: React.FC<StatusViewProps> = ({
  statusOptions,
  displayType,
  selectedValue,
  sx,
  onHoverViewActions,
  manualCellEdit,
  onClearValue,
}) => {
  const theme = useTheme();
  const type = displayType ?? StatusDisplayTypes.Fill;

  const optionComponents = {
    [StatusDisplayTypes.LeftIndicator]: {
      item: LeftIndicatorStatusItem,
      menu: StyledViewLeftIndicatorStatusItem,
      menuBackground: theme.palette.common.white,
    },
    [StatusDisplayTypes.Classic]: {
      item: ClassicStatusItem,
      menu: StyledViewClassicStatusItem,
      menuBackground: theme.palette.background.default,
    },
    [StatusDisplayTypes.Fill]: {
      item: FillStatusItem,
      menu: StyledViewFillStatusItem,
      menuBackground: theme.palette.common.white,
    },
    [StatusDisplayTypes.Round]: {
      item: RoundStatusItem,
      menu: StyledViewRoundStatusItem,
      menuBackground: theme.palette.common.white,
    },
  };

  const { item: OptionItem, menu: OptionMenuItem } = optionComponents[type];

  const getSelectedOption = () => {
    if (selectedValue && statusOptions) {
      const selectedIndex = statusOptions.findIndex(option => option.id !== undefined && option.id === selectedValue);

      return statusOptions[selectedIndex];
    }

    return null;
  };

  const selectedOption = getSelectedOption();

  return (
    <StyledStatusView sx={sx}>
      {selectedOption && (
        <OptionMenuItem
          key={selectedOption.id}
          color={selectedOption.color}
          className='status-view-item'
        >
          <StatusValueWrapper>
            <OptionItem
              viewMode={true}
              option={selectedOption}
            />
          </StatusValueWrapper>
          {onHoverViewActions && (
            <OnHoverStatusActions
              onClearValue={onClearValue}
              manualCellEdit={manualCellEdit}
              selectedValue={!!selectedValue}
            />
          )}
        </OptionMenuItem>
      )}
      {!selectedOption && (
        <StatusValueWrapper>
          {onHoverViewActions && (
            <OnHoverStatusActions
              onClearValue={onClearValue}
              manualCellEdit={manualCellEdit}
              selectedValue={!!selectedValue}
            />
          )}
        </StatusValueWrapper>
      )}
    </StyledStatusView>
  );
};
