import React from 'react';

import type { StatusItemProps } from '../types';
import { ClassicStatusBubble, ClassicStatusItemContainer, StyledOptionTypography } from './StyledComponents';

export const ClassicStatusItem: React.FC<StatusItemProps> = ({ option }) => {
  return (
    <ClassicStatusItemContainer>
      <ClassicStatusBubble color={option.color}>
        <StyledOptionTypography color={option.color}>{option.label} </StyledOptionTypography>
      </ClassicStatusBubble>
    </ClassicStatusItemContainer>
  );
};
