import { useCallback, useEffect, useRef, useState } from 'react';

import { convertCoverOffset } from '../..';
import { useResizeObserver } from './useResizeObserver';

interface ContainerDimensions {
  width: number;
  height: number;
}
interface AssetDimensions {
  width: number;
  height: number;
}
interface Position {
  y: number;
}
interface UseContainerResizeOptions {
  debounceTime?: number;
  onResize?: (position: Position) => void;
}
export const useCoverContainerResize = (
  containerRef: React.RefObject<HTMLElement>,
  imageRef: React.RefObject<HTMLImageElement>,
  assetContainerDimensions: AssetDimensions | null,
  assetPosition: Position | null,
  url: string | null,
  options?: UseContainerResizeOptions
) => {
  const { debounceTime = 100, onResize } = options || {};
  const [containerDimensions, setContainerDimensions] = useState<ContainerDimensions | null>(null);
  const [width, height] = useResizeObserver(containerRef, { debounceTime });
  const [isLoading, setIsLoading] = useState(true);

  const onResizeRef = useRef(onResize);
  onResizeRef.current = onResize;

  const [position, setPosition] = useState<Position>(() => {
    return assetPosition || { y: 0 };
  });

  useEffect(() => {
    if (url) {
      setIsLoading(true);
    }
  }, [url]);

  useEffect(() => {
    if (!url) {
      setIsLoading(false);
    }
  }, [url]);

  const updatePosition = useCallback(() => {
    if (!containerRef.current) {
      return;
    }

    const { clientWidth, clientHeight } = containerRef.current;

    setContainerDimensions(prev => {
      if (!prev || prev.width !== clientWidth || prev.height !== clientHeight) {
        return { width: clientWidth, height: clientHeight };
      }

      return prev;
    });

    if (
      imageRef.current?.naturalWidth &&
      imageRef.current?.naturalHeight &&
      assetContainerDimensions?.width &&
      assetContainerDimensions?.height &&
      assetPosition?.y !== undefined
    ) {
      const newPosition = convertCoverOffset({
        imageWidth: imageRef.current.naturalWidth,
        imageHeight: imageRef.current.naturalHeight,
        originalContainerWidth: assetContainerDimensions.width,
        originalContainerHeight: assetContainerDimensions.height,
        containerWidth: clientWidth,
        containerHeight: clientHeight,
        offset: assetPosition?.y,
      });

      const scaledHeight = (imageRef.current.naturalHeight / imageRef.current.naturalWidth) * clientWidth;

      if (scaledHeight <= clientHeight) {
        setPosition({ y: -0 });
      } else {
        const newPositionObj: Position = { y: newPosition };

        setPosition(prev => {
          const diff = Math.abs(prev.y - newPositionObj.y);
          if (prev.y === 0 || diff > 10) {
            if (onResizeRef.current) {
              onResizeRef.current(newPositionObj);
            }

            return newPositionObj;
          }

          return prev;
        });
      }
    }

    setIsLoading(false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [assetContainerDimensions, assetPosition]);

  useEffect(() => {
    if (width > 0 && height > 0) {
      updatePosition();
    }
  }, [width, height, updatePosition]);

  useEffect(() => {
    const img = imageRef.current;
    if (!img) {
      return;
    }

    const handleLoad = () => {
      updatePosition();
    };

    if (img.complete && img.naturalWidth > 0) {
      handleLoad();
    } else {
      img.addEventListener('load', handleLoad);

      return () => {
        img.removeEventListener('load', handleLoad);
      };
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [url, updatePosition]);

  const refreshPosition = useCallback(() => {
    updatePosition();
  }, [updatePosition]);

  const setDynamicPosition = useCallback((newY: number) => {
    const newPos = { y: newY };
    setPosition(prev => {
      if (prev.y !== newY) {
        if (onResizeRef.current) {
          onResizeRef.current(newPos);
        }

        return newPos;
      }

      return prev;
    });
  }, []);

  return {
    position,
    containerDimensions,
    refreshPosition,
    setDynamicPosition,
    isLoading,
  };
};
