import { PuiIcon, PuiSvgIcon } from '../../../..';
import { HoverIconsWrapper, HoverIconsWrapperContainer } from './StyledComponents';

export interface OnHoverSelectActionsProps {
  selectedValue: boolean;
  onClearValue?: () => void;
  manualCellEdit?: () => void;
}

export const OnHoverSelectActions: React.FC<OnHoverSelectActionsProps> = ({
  onClearValue,
  manualCellEdit,
  selectedValue,
}) => {
  return (
    <HoverIconsWrapperContainer className='hover-select-actions'>
      <HoverIconsWrapper>
        {selectedValue && (
          <PuiSvgIcon
            width={14}
            height={14}
            icon={PuiIcon.XClose}
            onClick={onClearValue}
          />
        )}
        <PuiSvgIcon
          width={14}
          height={14}
          onClick={manualCellEdit}
          icon={PuiIcon.ArrowDropDown}
        />
      </HoverIconsWrapper>
    </HoverIconsWrapperContainer>
  );
};
