import { useCallback, useRef, useState } from 'react';

import { PuiIcon } from '../../..';
import { useTheme } from '../../../hooks';
import type { PuiMultipleEmailsProps } from '../types';
import { EmailPopupMenu } from './EmailPopupMenu';
import {
  EmailItemWrapper,
  EndSvgIcon,
  MultipleEmailsWrapper,
  StartSvgIcon,
  StyledPuiTypography,
} from './StyledComponents';

export const PuiMultipleEmails: React.FC<PuiMultipleEmailsProps> = ({
  values,
  disabled,
  wrapperSx,
  emailWrapperSx,
  popupMenuSx,
  onSave,
}) => {
  const theme = useTheme();

  const [editMultipleEmail, setEditMultipleEmail] = useState('');
  const [anchorEl, setAnchorEl] = useState(null);
  const anchorRef = useRef(null);

  const isOpened = Boolean(anchorEl);

  const handleSave = useCallback(
    (newValue: string) => {
      const changedEmailIndex = values.findIndex(item => item === editMultipleEmail);
      const newEmails = [...values];

      if (changedEmailIndex === -1 && newValue) {
        newEmails.push(newValue);
      }

      if (changedEmailIndex !== -1 && newValue) {
        newEmails[changedEmailIndex] = newValue;
      }

      if (changedEmailIndex !== -1 && !newValue) {
        newEmails.splice(changedEmailIndex, 1);
      }

      onSave(!newEmails.every(v => v === '') ? newEmails : null);
    },
    [editMultipleEmail, onSave, values]
  );

  const handleCloseMenu = useCallback(() => {
    setEditMultipleEmail('');
    setAnchorEl(null);
  }, []);

  const handleOpenMenu = (editingEmail: string) => {
    if (disabled) return;
    setEditMultipleEmail(editingEmail);
    setAnchorEl(anchorRef.current);
  };

  const handleRemoveEmail = (removingEmail: string) => {
    if (disabled) return;
    const newEmails = values.filter(item => item !== removingEmail);

    onSave(newEmails);
  };

  return (
    <MultipleEmailsWrapper
      sx={wrapperSx}
      ref={anchorRef}
    >
      {values.map((item: string, index: number) => (
        <EmailItemWrapper
          sx={emailWrapperSx}
          key={item}
        >
          {index === 0 && !disabled && (
            <StartSvgIcon
              icon={PuiIcon.PlusCircle}
              width={20}
              height={20}
              stroke={theme.palette.primary.main}
              aria-label='add'
              onClick={() => handleOpenMenu('')}
            />
          )}
          <StyledPuiTypography
            variant='body-sm-regular'
            key={item}
          >
            {item}
          </StyledPuiTypography>
          {!disabled && (
            <>
              <EndSvgIcon
                icon={PuiIcon.Pencil}
                width={12}
                height={12}
                stroke={theme.palette.grey[300]}
                aria-label='edit'
                onClick={() => handleOpenMenu(item)}
              />
              <EndSvgIcon
                icon={PuiIcon.XClose}
                width={16}
                height={16}
                stroke={theme.palette.grey[300]}
                aria-label='delete'
                onClick={() => handleRemoveEmail(item)}
              />
            </>
          )}
        </EmailItemWrapper>
      ))}
      <EmailPopupMenu
        isOpened={isOpened}
        initialValue={editMultipleEmail}
        anchorEl={anchorEl}
        popupMenuSx={popupMenuSx}
        onCloseMenu={handleCloseMenu}
        onSave={handleSave}
      />
    </MultipleEmailsWrapper>
  );
};
