import type { ChangeEvent } from 'react';
import { useCallback, useLayoutEffect, useRef, useState } from 'react';

import { makeCaretFocusHandler } from '../../utils';
import type { PuiTextEditProps } from '../types';
import { CustomTextInput, TextInputContainer } from './StyledComponents';

export const PuiTextEdit: React.FC<PuiTextEditProps> = ({
  value,
  wrap,
  disabled,
  isLoading,
  wrapperSx,
  inputSx,
  validationErrorMsg,
  placeholder = '',
  autoFocus = false,
  onResetValidationError,
  onChange,
}) => {
  const [text, setText] = useState<string>(value);
  const [isOverflowed, setIsOverflowed] = useState(false);
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const handleFocus = makeCaretFocusHandler(true);

  const handleChange = useCallback(
    (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
      const value = e.currentTarget.value !== '' ? e.currentTarget.value : null;
      !isLoading && setText(e.currentTarget.value);
      !isLoading && typeof onChange === 'function' && onChange(value);
      validationErrorMsg !== null && onResetValidationError();
    },
    [isLoading, validationErrorMsg, onResetValidationError, onChange]
  );

  useLayoutEffect(() => {
    if (!wrap || !textareaRef.current) {
      return;
    }

    const textAreaElement = textareaRef.current;
    const handleWheelCapture = (event: WheelEvent) => {
      event.stopPropagation();
    };

    textAreaElement.addEventListener('wheel', handleWheelCapture);

    const updateOverflowState = () => {
      setIsOverflowed(textAreaElement.scrollHeight > textAreaElement.clientHeight);
    };

    updateOverflowState();

    const resizeObserver = new ResizeObserver(updateOverflowState);
    resizeObserver.observe(textAreaElement);

    return () => {
      textAreaElement.removeEventListener('wheel', handleWheelCapture);
      resizeObserver.disconnect();
    };
  }, [wrap, text]);

  let inputClassName: string | undefined;
  if (wrap) {
    inputClassName = 'wrapper';
    if (isOverflowed) {
      inputClassName += ' overflowed';
    }
  } else {
    inputClassName = undefined;
  }

  return (
    <TextInputContainer
      className={wrap ? 'no-wrap' : ''}
      sx={wrapperSx}
    >
      <CustomTextInput
        inputRef={textareaRef}
        className={inputClassName}
        type='textarea'
        size='small'
        value={text}
        multiline={wrap}
        error={!!validationErrorMsg}
        maxRows={12}
        disabled={disabled}
        sx={inputSx}
        autoFocus={autoFocus}
        onChange={handleChange}
        placeholder={placeholder}
        onFocus={handleFocus}
      />
    </TextInputContainer>
  );
};
