import type { FC } from 'react';

import { ComponentMode } from '../../types';
import { PuiEmailEdit } from './edit/EmailEdit';
import type { EmailProps } from './types';
import { PuiEmailView } from './view/EmailView';

export const PuiEmail: FC<EmailProps> = ({
  values,
  multiple = false,
  mode = ComponentMode.Edit,
  disabled = false,
  ...restProps
}) => {
  if (mode === ComponentMode.View) {
    return (
      <PuiEmailView
        value={values[0] ?? ''}
        restItemsCount={multiple && values.length > 1 ? values.length - 1 : 0}
      />
    );
  }

  if (mode === ComponentMode.Edit) {
    return (
      <PuiEmailEdit
        values={values}
        multiple={multiple}
        disabled={disabled}
        {...restProps}
      />
    );
  }

  return null;
};
