import { useTheme } from '@mui/material';

import type { PuiSwitchViewProps } from '../types';
import { StyledSwitchBackground, StyledSwitchContent, StyledViewSwitchContainer } from './StyledComponents';

export const PuiSwitchView: React.FC<PuiSwitchViewProps> = ({ checked, color, sx }) => {
  const theme = useTheme();

  return (
    <StyledViewSwitchContainer sx={sx}>
      <StyledSwitchBackground
        color={checked ? color : theme.palette.grey[50]}
        checked={checked}
      >
        <StyledSwitchContent></StyledSwitchContent>
      </StyledSwitchBackground>
    </StyledViewSwitchContainer>
  );
};
