import type { NumberFormatEnum } from '../../number/types';
import { formatNumber } from '../../utils';
import { currencies } from '../currencies';
import type { PuiCurrencyViewProps } from '../types';
import { TextField, TextInputContainer } from './StyledComponents';

export const PuiCurrencyView: React.FC<PuiCurrencyViewProps> = ({ value, defaultCurrency }) => {
  const currency = currencies[defaultCurrency];
  const currencySymbol = (value && currency?.symbol) || '';

  return (
    <TextInputContainer>
      <TextField variant='body-sm-regular'>
        {currencySymbol} {formatNumber(value, 'space-period' as NumberFormatEnum, 2)}
      </TextField>
    </TextInputContainer>
  );
};
