import type { PopperProps } from '@mui/material';

type ModifierCfg = NonNullable<PopperProps['modifiers']>;

export const POPPER_PRESET_DEFAULT: Partial<PopperProps> & {
  modifiers: ModifierCfg;
} = {
  placement: 'bottom-start',
  disablePortal: true,
  transition: true,

  modifiers: [
    { name: 'offset', options: { offset: [0, 4] } },

    { name: 'flip', options: { padding: 8, rootBoundary: 'document' } },

    { name: 'preventOverflow', options: { mainAxis: false, altAxis: false, tether: false, padding: 8 } },

    { name: 'computeStyles', options: { gpuAcceleration: true, adaptive: false } },
  ] satisfies ModifierCfg,
};
