import type { BoxProps, InputProps } from '@mui/material';
import type { FC } from 'react';

import { BaseSlider, type BaseSliderProps } from '../common/BaseSlider';
import { InputViewWrapper, PercentageSign, ValueText, Wrapper } from '../StyledComponents';

interface PuiSliderViewProps extends BaseSliderProps {
  inputWrapperSx?: BoxProps['sx'];
  inputSx?: InputProps['sx'];
}

export const PuiSliderView: FC<PuiSliderViewProps> = ({ value, percentage = false, inputWrapperSx, ...props }) => {
  return (
    <Wrapper>
      <BaseSlider
        isViewMode
        value={value}
        percentage={percentage}
        {...props}
      />
      <InputViewWrapper sx={inputWrapperSx}>
        <ValueText>{value === -1 ? 0 : value}</ValueText>
        <PercentageSign sx={{ opacity: percentage ? 1 : 0 }}>%</PercentageSign>
      </InputViewWrapper>
    </Wrapper>
  );
};
