import type { LinkProps, TypographyProps } from '@mui/material';

import { formatUrl, type SocialMediaDomain } from '../../..';
import { PuiTypography } from '../Typography';
import { PuiSocialMediaIcon } from './SocialMediaIcon';
import { ListItemLink } from './StyledComponents';

interface PuiContactSocialMediaProps {
  domain: SocialMediaDomain | null;
  link: string;
  iconOnly?: boolean;
  sx?: LinkProps['sx'];
  variant?: TypographyProps['variant'];
}

export const PuiContactSocialMediaItem: React.FC<PuiContactSocialMediaProps> = ({
  domain,
  link,
  iconOnly,
  sx,
  variant,
}) => {
  return (
    <ListItemLink
      href={formatUrl(link)}
      target='_blank'
      rel='noopener noreferrer'
      underline='none'
      sx={sx}
    >
      <PuiSocialMediaIcon domain={domain} />
      {!iconOnly && (
        <PuiTypography variant={variant ?? 'body-sm-medium'}>
          {domain ? domain.charAt(0).toUpperCase() + domain.slice(1) : link}
        </PuiTypography>
      )}
    </ListItemLink>
  );
};
