import type { PuiPhoneEditProps } from '../types';
import { MultiplePhones } from './MultiplePhones';
import { SinglePhone } from './SinglePhone';

export const PuiPhoneEdit: React.FC<PuiPhoneEditProps> = ({
  value,
  values,
  showMultiple = false,
  multiple = false,
  singlePhoneWrapperSx,
  multiplePhoneWrapperSx,
  phoneItemWrapperSx,
  onChange,
  ...restProps
}) => {
  return !showMultiple ? (
    <SinglePhone
      value={value}
      multiple={multiple}
      onChange={onChange}
      singlePhoneWrapperSx={singlePhoneWrapperSx}
      {...restProps}
    />
  ) : (
    <MultiplePhones
      values={values}
      phoneItemWrapperSx={phoneItemWrapperSx}
      multiplePhoneWrapperSx={multiplePhoneWrapperSx}
      {...restProps}
    />
  );
};
