import type { PuiStatusEditProps } from '../types';
import { StatusDropdown } from './StatusDropdown';

export const PuiStatusEdit: React.FC<PuiStatusEditProps> = ({
  options = [],
  disabled,
  value,
  displayType,
  open = false,
  handleChanges,
  className,
  onMenuOpen,
  onMenuClose,
  placeholder,
  sx,
}) => {
  return (
    <StatusDropdown
      disabled={disabled}
      onValueChange={handleChanges}
      statusOptions={options}
      selectedValue={value}
      displayType={displayType}
      open={open}
      className={className}
      onMenuOpen={onMenuOpen}
      onMenuClose={onMenuClose}
      placeholder={placeholder}
      sx={sx}
    />
  );
};
