import type { FC } from 'react';
import { useCallback, useState } from 'react';

import { BaseTagCell } from '../common/BaseTagCell';
import type { TagCellBaseProps } from '../types';
import { SelectedOptionsView } from './SelectedOptionsView';

export const PuiTagCellView: FC<TagCellBaseProps> = ({
  displayType,
  selectedValues,
  currentColumnWidth,
  disabled,
  tagCellSx,
  optionSx,
  chipSx = {},
  placeholder,
  hideTransparentItems,
}) => {
  const [isTooltipOpen, setIsTooltipOpen] = useState(false);
  const [remainingCount, setRemainingCount] = useState(0);

  const onTooltipClose = useCallback(() => {
    setIsTooltipOpen(false);
  }, []);

  const onTooltipOpen = useCallback(() => {
    if (remainingCount > 0) {
      setIsTooltipOpen(true);
    }
  }, [remainingCount]);

  return (
    <BaseTagCell
      selectedValues={selectedValues}
      disabled={disabled}
      tagCellSx={tagCellSx}
      isTooltipOpen={isTooltipOpen}
      onTooltipClose={onTooltipClose}
      onTooltipOpen={onTooltipOpen}
    >
      <SelectedOptionsView
        selectedValues={selectedValues.map(({ title }) => title)}
        displayType={displayType}
        showCircleButton={false}
        currentColumnWidth={currentColumnWidth}
        optionSx={optionSx}
        chipSx={chipSx}
        remainingCount={remainingCount}
        setRemainingCount={setRemainingCount}
        placeholder={placeholder}
        hideTransparentItems={hideTransparentItems}
      />
    </BaseTagCell>
  );
};
