import { useCallback } from 'react';

import { PuiAvatar, PuiBox, PuiTooltip } from '../../../components';
import type { PuiUser } from '../../../types';
import { TOOLTIP_ENTER_DELAY } from '../constants';
import { PuiSurplus } from '../PuiSurplus';
import { PuiTooltipTitle } from '../PuiTooltipTitle';
import { StyledPersonsAvatarGroup } from '../StyledComponents';

type PuiPersonAvatarProps = {
  user: PuiUser;
  selectedUsers: PuiUser[];
};

export const PuiPersonAvatar: React.FC<PuiPersonAvatarProps> = ({ user, selectedUsers }) => {
  return (
    <PuiTooltip
      enterDelay={TOOLTIP_ENTER_DELAY}
      placement='top'
      arrow
      title={
        user.id !== selectedUsers[selectedUsers.length - 1].id ? (
          <PuiTooltipTitle user={user} />
        ) : (
          <PuiTooltipTitle user={selectedUsers[selectedUsers.length - 1]} />
        )
      }
      PopperProps={{
        modifiers: [
          {
            name: 'offset',
            options: {
              offset: [0, -9],
            },
          },
        ],
        sx: {
          '& .MuiTooltip-tooltip': {
            backgroundColor: 'common.white',
            color: 'grey.400',
            boxShadow: '0px 4px 12px rgba(0, 0, 0, 0.1)',
            padding: '8px 12px',
            fontSize: '12px',
          },
          '& .MuiTooltip-arrow': {
            color: 'common.white',
          },
        },
      }}
    >
      <PuiBox>
        <PuiAvatar
          key={user.id}
          contact={user}
        />
      </PuiBox>
    </PuiTooltip>
  );
};

interface PuiPersonViewProps {
  selectedUsers: PuiUser[];
  maxAvatars?: number;
}

export const PuiPersonView: React.FC<PuiPersonViewProps> = ({ selectedUsers, maxAvatars = 7 }) => {
  const renderSurplus = useCallback(() => {
    if (selectedUsers.length <= maxAvatars) {
      return null;
    }
    const surplusCount = selectedUsers.length - (maxAvatars - 1);
    const hiddenUsers = selectedUsers.slice(-surplusCount);

    return <PuiSurplus hiddenUsers={hiddenUsers} />;
  }, [selectedUsers, maxAvatars]);

  return (
    <StyledPersonsAvatarGroup
      max={maxAvatars}
      renderSurplus={renderSurplus}
      sx={{ '& .MuiAvatar-root': { margin: 0, marginRight: '-8px' } }}
    >
      {selectedUsers.map((user: PuiUser) => (
        <PuiPersonAvatar
          key={user.id}
          user={user}
          selectedUsers={selectedUsers}
        />
      ))}
    </StyledPersonsAvatarGroup>
  );
};
