import { PuiAvatar } from '../../../components';
import type { PuiUser } from '../../../types';
import { StyledPersonsAvatarGroup, StyledPlaceholder } from '../StyledComponents';

interface PuiPersonEditProps {
  placeholder?: string;
  selectedUsers: PuiUser[];
  maxAvatars?: number;
}

export const PuiPersonEdit: React.FC<PuiPersonEditProps> = ({ selectedUsers, placeholder, maxAvatars = 7 }) => {
  return (
    <StyledPersonsAvatarGroup max={maxAvatars}>
      {selectedUsers.length > 0
        ? selectedUsers.map((user: PuiUser) => {
            return (
              <PuiAvatar
                key={user.id}
                contact={user}
              />
            );
          })
        : placeholder && (
            <StyledPlaceholder
              variant='body-sm-regular'
              noWrap
            >
              {placeholder}
            </StyledPlaceholder>
          )}
    </StyledPersonsAvatarGroup>
  );
};
