import type { FC } from 'react';

import { ComponentMode } from '../../types';
import { PuiSliderEdit } from './edit/SliderEdit';
import type { PuiSliderProps } from './types';
import { PuiSliderView } from './view/SliderView';

export const PuiSlider: FC<PuiSliderProps> = ({
  setValue,
  value,
  min = 0,
  max = 100,
  percentage = false,
  disabled = false,
  inputWrapperSx,
  inputSx,
  mode = ComponentMode.Edit,
  ...props
}) => {
  if (mode === ComponentMode.View) {
    return (
      <PuiSliderView
        value={value}
        percentage={percentage}
        {...props}
        disabled={disabled}
      />
    );
  }

  if (mode === ComponentMode.Edit) {
    return (
      <PuiSliderEdit
        setValue={setValue}
        value={value}
        min={min}
        max={max}
        percentage={percentage}
        disabled={disabled}
        inputWrapperSx={inputWrapperSx}
        inputSx={inputSx}
        {...props}
      />
    );
  }

  return null;
};
