import React from 'react';

import { PuiAvatar, PuiTypography } from '../../components';
import { PuiIcon } from '../../icons';
import type { PuiUser } from '../../types';
import {
  EmailBoxStyled,
  PhoneBoxStyled,
  ProfileButtonStyled,
  StyledAvatarBox,
  StyledAvatarIcon,
  StyledUserInfoBox,
  StyledUserInfoBoxContainer,
  StyledUserInfoBoxWrapper,
} from './StyledComponents';

export interface IPuiTooltipTitleProps {
  user: PuiUser;
}

export const PuiTooltipTitle: React.FC<IPuiTooltipTitleProps> = ({ user }) => {
  return (
    <StyledUserInfoBoxWrapper>
      <StyledUserInfoBoxContainer>
        <StyledAvatarBox>
          <PuiAvatar
            size={56}
            contact={user}
          />
        </StyledAvatarBox>
        <StyledUserInfoBox>
          <PuiTypography variant='body-lg-semibold'>{`${user.name}`}</PuiTypography>
          {user.details && <PuiTypography variant='body-xs-medium'>{user.details}</PuiTypography>}
          {user.email && (
            <EmailBoxStyled>
              <StyledAvatarIcon
                width={12}
                height={12}
                icon={PuiIcon.Mail2}
              />
              <PuiTypography variant='body-xs-medium'>{user.email}</PuiTypography>
            </EmailBoxStyled>
          )}
          {user.phone && (
            <PhoneBoxStyled>
              <StyledAvatarIcon
                width={12}
                height={12}
                icon={PuiIcon.Phone}
              />
              <PuiTypography variant='body-xs-medium'>{user.phone}</PuiTypography>
            </PhoneBoxStyled>
          )}
          <ProfileButtonStyled variant='outlined'>View Profile</ProfileButtonStyled>
        </StyledUserInfoBox>
      </StyledUserInfoBoxContainer>
    </StyledUserInfoBoxWrapper>
  );
};
