import type React from 'react';

import { SingleSelectClassicItem } from './edit/SingleSelectClassicItem';
import { SingleSelectIconWithTextItem } from './edit/SingleSelectIconWithTextItem';
import { SingleSelectOnlyTextItem } from './edit/SingleSelectOnlyTextItem';
import { SingleSelectRoundIndicatorItem } from './edit/SingleSelectRoundIndicatorItem';
import type { SingleSelectItemProps } from './types';
import { SingleSelectDisplayTypes } from './types';

export const getSingleSelectOptionComponent = (displayType?: SingleSelectDisplayTypes) => {
  const type = displayType ?? SingleSelectDisplayTypes.Classic;

  const optionComponents: Record<SingleSelectDisplayTypes, React.FC<SingleSelectItemProps>> = {
    [SingleSelectDisplayTypes.Classic]: SingleSelectClassicItem,
    [SingleSelectDisplayTypes.OnlyText]: SingleSelectOnlyTextItem,
    [SingleSelectDisplayTypes.IconWithText]: SingleSelectIconWithTextItem,
    [SingleSelectDisplayTypes.RoundIndicator]: SingleSelectRoundIndicatorItem,
  };

  return optionComponents[type];
};
