import type { FC } from 'react';

import { useTheme } from '../../hooks';
import type { CustomIconProps } from './types';

export const Radiolabel: FC<CustomIconProps> = ({ width = 16, height = 16, fill }) => {
  const theme = useTheme();
  const baseColor = fill ?? theme.palette.grey[300];

  return (
    <svg
      xmlns='http://www.w3.org/2000/svg'
      width={width}
      height={height}
      viewBox='0 0 16 16'
      fill='none'
    >
      <rect
        x='0.5'
        y='15.5'
        width='15'
        height='15'
        rx='7.5'
        transform='rotate(-90 0.5 15.5)'
        fill={baseColor}
        stroke={baseColor}
      />
      <rect
        x='4'
        y='12'
        width='8'
        height='8'
        rx='4'
        transform='rotate(-90 4 12)'
        fill='white'
      />
    </svg>
  );
};
