import type { EmailProps } from '../types';
import { PuiMultipleEmails } from './MultipleEmails';
import { PuiSingleEmail } from './SingleEmail';

export const PuiEmailEdit: React.FC<EmailProps> = ({
  values,
  multiple = false,
  anchorEl,
  disabled,
  inputSx,
  singleEmailWrapperSx,
  multipleEmailsWrapperSx,
  popupMenuSx,
  emailWrapperSx,
  onReset,
  onSave,
  onChange,
  autoFocus,
  placeholder,
}) => {
  const hasValues = Array.isArray(values) && values.length > 0;
  const showMultiple = hasValues && values.length > 1;
  const singleValue = hasValues ? values[0] : '';

  return !showMultiple ? (
    <PuiSingleEmail
      value={singleValue}
      multiple={multiple}
      anchorEl={anchorEl}
      disabled={disabled}
      wrapperSx={singleEmailWrapperSx}
      inputSx={inputSx}
      popupMenuSx={popupMenuSx}
      onSave={onSave}
      onReset={onReset}
      onChange={onChange}
      autoFocus={autoFocus}
      placeholder={placeholder}
    />
  ) : (
    <PuiMultipleEmails
      values={values}
      disabled={disabled}
      wrapperSx={multipleEmailsWrapperSx}
      emailWrapperSx={emailWrapperSx}
      popupMenuSx={popupMenuSx}
      onSave={onSave}
      onReset={onReset}
    />
  );
};
