import React from 'react';

import type { StatusItemProps } from '../types';
import { RoundIndicator, RoundOptionContainer, StyledOptionTypography } from './StyledComponents';

export const RoundStatusItem: React.FC<StatusItemProps> = ({ option, viewMode }) => {
  return (
    <RoundOptionContainer className='round-option'>
      <StyledOptionTypography>{option.label}</StyledOptionTypography>
      <RoundIndicator
        color={option.color}
        className={`round-indicator ${viewMode ? 'view' : ''}`}
      />
    </RoundOptionContainer>
  );
};
