import { PuiTooltip } from '../../../';
import type { PuiPhoneViewProps } from '../types';
import { PhoneViewInput } from './PhoneViewInput';
import { RestPhonesCount, TextViewWrapper, TooltipText } from './StyledComponents';

export const PuiPhoneView: React.FC<PuiPhoneViewProps> = ({
  value,
  restValues,
  defaultCountry,
  textViewWrapperSx,
  inputSx,
}) => {
  if (!value.phone) {
    return null;
  }

  const renderContent = () => {
    if (restValues.length !== 0) {
      return (
        <PuiTooltip
          title={<TooltipText variant='body-sm-medium'>{restValues.map(item => item.phone).join(', ')}</TooltipText>}
          placement='top'
          arrow
        >
          <TextViewWrapper sx={textViewWrapperSx}>
            <PhoneViewInput
              value={value.phone}
              defaultCountry={value.countryCode || defaultCountry}
              sx={inputSx}
            />
            {restValues.length > 0 && <RestPhonesCount>+{restValues.length}</RestPhonesCount>}
          </TextViewWrapper>
        </PuiTooltip>
      );
    }

    return (
      <PhoneViewInput
        value={value.phone}
        defaultCountry={value.countryCode || defaultCountry}
        sx={inputSx}
      />
    );
  };

  return renderContent();
};
