import { PuiIcon, PuiSvgIcon } from '../../../..';
import { HoverIconsWrapper, HoverIconsWrapperContainer } from './StyledComponents';

export interface OnHoverStatusActionsProps {
  selectedValue: boolean;
  onClearValue?: () => void;
  manualCellEdit?: () => void;
}

export const OnHoverStatusActions: React.FC<OnHoverStatusActionsProps> = ({
  onClearValue,
  manualCellEdit,
  selectedValue,
}) => {
  return (
    <HoverIconsWrapperContainer className='hover-status-actions'>
      <HoverIconsWrapper>
        {selectedValue && (
          <PuiSvgIcon
            width={14}
            height={14}
            icon={PuiIcon.XClose}
            onClick={onClearValue}
          />
        )}
        <PuiSvgIcon
          width={14}
          height={14}
          onClick={manualCellEdit}
          icon={PuiIcon.ArrowDropDown}
        />
      </HoverIconsWrapper>
    </HoverIconsWrapperContainer>
  );
};
