import React from 'react';

import { PuiTooltip } from '../../components';
import type { PuiUser } from '../../types';
import { TOOLTIP_ENTER_DELAY } from './constants';

interface PuiSurplusProps {
  hiddenUsers: PuiUser[];
}

export const PuiSurplus: React.FC<PuiSurplusProps> = ({ hiddenUsers }) => {
  const tooltipContent = hiddenUsers.map(user => `${user.name}`).join(', ');

  return (
    <PuiTooltip
      title={tooltipContent}
      placement='top'
      arrow
      enterDelay={TOOLTIP_ENTER_DELAY}
      PopperProps={{
        sx: {
          '& .MuiTooltip-tooltip': {
            backgroundColor: 'common.white',
            color: 'grey.400',
            boxShadow: '0px 4px 12px rgba(0, 0, 0, 0.1)',
            padding: '8px 12px',
            fontSize: '12px',
          },
          '& .MuiTooltip-arrow': {
            color: 'common.white',
          },
        },
      }}
    >
      <div>+{hiddenUsers.length}</div>
    </PuiTooltip>
  );
};
