import type { BoxProps, ChipProps } from '@mui/material';

import { PuiIcon, PuiSvgIcon } from '../../../icons';
import { BaseSelectedOptions } from '../common/BaseSelectedOptions';
import type { DisplayType, RenderChipsProps } from '../types';
import { getDropdownChipLabel } from '../utils';
import { RemainingCountChip, StyledChip } from './StyledComponents';

interface SelectedOptionsProps {
  isDropdownOpen: boolean;
  selectedValues: { id: string; title: string }[];
  displayType: DisplayType;
  showCircleButton: boolean;
  currentColumnWidth: number;
  optionSx?: BoxProps['sx'];
  chipSx?: ChipProps['sx'];
  remainingCount: number;
  setRemainingCount: (remainingCount: number) => void;
  onDelete: (deleteOptionValue: string) => void;
  placeholder?: string;
}

const SelectedOptions: React.FC<SelectedOptionsProps> = ({
  isDropdownOpen,
  selectedValues,
  displayType,
  showCircleButton,
  currentColumnWidth,
  optionSx,
  chipSx,
  remainingCount,
  setRemainingCount,
  onDelete,
  placeholder,
}) => {
  const renderChips = ({ visibleChips, hiddenChips }: RenderChipsProps) => (
    <>
      {visibleChips.map((value, idx) => (
        <StyledChip
          key={`${value}-${idx}`}
          label={getDropdownChipLabel(value)}
          displayType={displayType}
          sx={chipSx}
          className={isDropdownOpen ? 'enable-hover' : ''}
          onDelete={() => (isDropdownOpen ? onDelete(value) : null)}
          deleteIcon={
            <PuiSvgIcon
              icon={PuiIcon.XSm}
              width={10}
              height={10}
            />
          }
        />
      ))}
      {!isDropdownOpen ? (
        <RemainingCountChip
          key={`remaining-${remainingCount}`}
          label={`+${remainingCount}`}
          displayType={displayType}
          className='counter'
          sx={{ opacity: !isDropdownOpen && remainingCount > 0 ? 1 : 0 }}
        />
      ) : null}
      {hiddenChips.map(value => (
        <StyledChip
          key={`hidden-${value}`}
          label={getDropdownChipLabel(value)}
          displayType={displayType}
          className={isDropdownOpen ? 'enable-hover' : ''}
          sx={{
            opacity: isDropdownOpen ? 1 : 0,
            ...chipSx,
          }}
          onDelete={() => onDelete(value)}
          deleteIcon={
            <PuiSvgIcon
              icon={PuiIcon.XSm}
              width={10}
              height={10}
            />
          }
        />
      ))}
    </>
  );

  return (
    <BaseSelectedOptions
      selectedValues={selectedValues.map(({ title }) => title)}
      displayType={displayType}
      showCircleButton={showCircleButton}
      currentColumnWidth={currentColumnWidth}
      optionSx={optionSx}
      chipSx={chipSx}
      remainingCount={remainingCount}
      setRemainingCount={setRemainingCount}
      renderChips={renderChips}
      placeholder={placeholder}
    />
  );
};

export default SelectedOptions;
