import type { PuiUser } from '../../types';
import { ComponentMode } from '../../types';
import { PuiPersonEdit } from './edit';
import { PuiPersonView } from './view';

export interface PuiPersonProps {
  mode?: ComponentMode;
  selectedUsers: PuiUser[];
  maxAvatars?: number;
  placeholder?: string;
}

export const PuiPerson: React.FC<PuiPersonProps> = ({
  mode = ComponentMode.Edit,
  selectedUsers,
  maxAvatars,
  placeholder,
}) => {
  if (mode === ComponentMode.Edit) {
    return (
      <PuiPersonEdit
        selectedUsers={selectedUsers}
        maxAvatars={maxAvatars}
        placeholder={placeholder}
      />
    );
  }
  if (mode === ComponentMode.View) {
    return (
      <PuiPersonView
        selectedUsers={selectedUsers}
        maxAvatars={maxAvatars}
      />
    );
  }

  return null;
};
