import { useRef, useState } from 'react';

import { PuiIcon, useTheme } from '../../../';
import type { PhoneValueType, SinglePhoneProps } from '../types';
import { validatePhone } from '../validatePhone';
import { PhoneInput } from './PhoneInput';
import { PhonePopupMenu } from './PhonePopupMenu';
import { EndSvgIcon, SinglePhoneWrapper, StartSvgIcon } from './StyledComponents';

export const SinglePhone: React.FC<SinglePhoneProps> = ({
  value,
  defaultCountry,
  multiple,
  disabled,
  inputSx,
  popupSx,
  singlePhoneWrapperSx,
  onSave,
  onReset,
  onChange,
  autoFocus,
  placeholder,
  className,
}) => {
  const theme = useTheme();

  const [phone, setPhone] = useState<string>(value.phone);
  const [countryCode, setCountryCode] = useState<string>(value.countryCode);
  const [validationError, setValidationError] = useState<null | string>(null);
  const [isOpened, setIsOpened] = useState<boolean>(false);
  const anchorRef = useRef(null);

  const handleSaveNewPhone = (newValue: PhoneValueType) => {
    onSave([value, newValue]);
    setIsOpened(false);
  };

  const handleResetText = () => {
    setPhone(value.phone);
    setCountryCode(value.countryCode);
    setValidationError(null);
    onReset();
  };

  const handleChange = (newValue: string, newCountryCode: string) => {
    setPhone(newValue);
    const isValid = validatePhone(newValue, newCountryCode);
    const validationError = !isValid ? 'Enter a valid phone number' : null;

    typeof onChange === 'function' && onChange([{ phone: newValue, countryCode: newCountryCode }], { validationError });
    countryCode !== newCountryCode && setCountryCode(newCountryCode);
    validationError !== null && setValidationError(null);
  };

  const handleToggleMenu = () => setIsOpened(prevState => !prevState);

  const hasStartIcon = multiple && value.phone && !disabled;
  const hasEndIcon = validationError || value.phone !== phone;

  return (
    <SinglePhoneWrapper
      sx={singlePhoneWrapperSx}
      ref={anchorRef}
    >
      <PhoneInput
        value={phone}
        defaultCountry={defaultCountry}
        validationError={validationError}
        disabled={disabled}
        styleMod={hasStartIcon ? 'startIcons' : undefined}
        sx={inputSx}
        onChange={handleChange}
        autoFocus={autoFocus}
        placeholder={placeholder}
        className={className}
        InputProps={{
          endAdornment:
            hasStartIcon || hasEndIcon ? (
              <>
                {hasStartIcon && (
                  <StartSvgIcon
                    icon={PuiIcon.PlusCircle}
                    width={20}
                    height={20}
                    stroke={theme.palette.primary.main}
                    aria-label='add'
                    onClick={handleToggleMenu}
                  />
                )}
                {hasEndIcon && (
                  <EndSvgIcon
                    icon={PuiIcon.Refresh}
                    width={16}
                    height={16}
                    stroke={theme.palette.grey[300]}
                    aria-label='refresh'
                    onClick={handleResetText}
                  />
                )}
              </>
            ) : undefined,
        }}
      />
      <PhonePopupMenu
        isOpened={isOpened}
        initialValue=''
        initialCountryCode={defaultCountry}
        anchorEl={anchorRef.current}
        popupSx={popupSx}
        inputSx={inputSx}
        onCloseMenu={handleToggleMenu}
        onSave={handleSaveNewPhone}
      />
    </SinglePhoneWrapper>
  );
};
