import type { RefObject } from 'react';

interface IResult {
  showedCount: number;
  lastShowedElementIndex: number;
}
interface IParams {
  elements: RefObject<HTMLDivElement>[] | undefined;
  defaultElementWidthPx: number;
  containerClientWidth: number;
  displayShow?: string;
}

export const hideElementsWhenNoSpace = ({
  elements,
  defaultElementWidthPx,
  containerClientWidth,
  displayShow = 'inline-block',
}: IParams): IResult => {
  if (!containerClientWidth || !elements?.length) {
    return { showedCount: 0, lastShowedElementIndex: 0 };
  }

  let totalWidth = 0;
  let lastShowedElementIndex: null | number = null;
  let showedCount = 0;

  elements.forEach((element, index) => {
    if (!element.current) return;

    element.current.style.width = 'unset'; // unset width to get original element width

    const width = element.current.offsetWidth || defaultElementWidthPx;
    const possibleFilledWidth = width / 2 + totalWidth;
    const hasLastShowedElement = typeof lastShowedElementIndex === 'number';

    if (hasLastShowedElement || possibleFilledWidth > containerClientWidth) {
      element.current.style.display = 'none';

      if (!hasLastShowedElement) {
        lastShowedElementIndex = index === 0 ? 0 : index - 1;
      }
    } else {
      element.current.style.display = displayShow;
      showedCount += 1;

      const isNeedToSetAvailableWidth = width + totalWidth > containerClientWidth;

      if (isNeedToSetAvailableWidth) {
        element.current.style.width = `${containerClientWidth - totalWidth}px`;
        lastShowedElementIndex = index;
      }
    }

    totalWidth += width;
  });

  return {
    showedCount,
    lastShowedElementIndex: lastShowedElementIndex ?? 0,
  };
};
