import type { SnackbarOrigin } from '@mui/material/Snackbar';
import Snackbar from '@mui/material/Snackbar';
import type { FC } from 'react';

import { PuiIcon, PuiSvgIcon } from '..';
import { PuiAlert, PuiButton } from './';

export type { SnackbarOrigin };

interface Props {
  message: string;
  open: boolean;
  color: 'info' | 'success' | 'error' | 'warning';
  anchorOrigin?: SnackbarOrigin;
  autoHideDuration?: number;
  onClose: () => void;
  actionText?: string;
  onAction?: () => void;
}

export const PuiSnackbar: FC<Props> = ({
  message,
  open,
  color,
  onClose,
  actionText,
  onAction,
  anchorOrigin,
  autoHideDuration = 60000,
}) => {
  const icons = {
    info: (
      <PuiSvgIcon
        icon={PuiIcon.AlertCircle}
        width={20}
        height={20}
      />
    ),
    success: (
      <PuiSvgIcon
        icon={PuiIcon.CheckCircle}
        width={20}
        height={20}
      />
    ),
    error: (
      <PuiSvgIcon
        icon={PuiIcon.AlertCircle}
        width={20}
        height={20}
      />
    ),
    warning: (
      <PuiSvgIcon
        icon={PuiIcon.AlertTriangle}
        width={20}
        height={20}
      />
    ),
  };

  return (
    <Snackbar
      open={open}
      autoHideDuration={autoHideDuration}
      onClose={onClose}
      anchorOrigin={anchorOrigin}
    >
      <PuiAlert
        onClose={onClose}
        color={color}
        variant='standard'
        icon={icons[color]}
      >
        {message}
        {actionText && (
          <PuiButton
            onClick={onAction}
            color={color}
            variant='contained'
            size='small'
          >
            {actionText}
          </PuiButton>
        )}
      </PuiAlert>
    </Snackbar>
  );
};
