import React from 'react';

import { useTheme } from '../../../../ui';
import type { SingleSelectItemProps } from '../types';
import { ClassicSingleSelectBubble, SingleSelectContainer, StyledSingleSelectTypography } from './StyledComponents';

export const SingleSelectClassicItem: React.FC<SingleSelectItemProps> = ({ option }) => {
  const theme = useTheme();

  return (
    <SingleSelectContainer>
      <ClassicSingleSelectBubble color={option.color}>
        <StyledSingleSelectTypography color={theme.palette.background.paper}>
          {option.label}
        </StyledSingleSelectTypography>
      </ClassicSingleSelectBubble>
    </SingleSelectContainer>
  );
};
