import { useEffect, useRef, useState } from 'react';

import { calculateChipVisibility } from '../utils';

interface UseChipVisibilityProps {
  selectedValues: string[];
  parentRef: React.RefObject<HTMLDivElement>;
  currentColumnWidth: number;
  setRemainingCount: (count: number) => void;
}

export const useChipVisibility = ({
  selectedValues,
  parentRef,
  currentColumnWidth,
  setRemainingCount,
}: UseChipVisibilityProps) => {
  const [visibleChips, setVisibleChips] = useState<string[]>(selectedValues);
  const [prevSelectedValues, setPrevSelectedValues] = useState<string[]>([]);
  const [hiddenChips, setHiddenChips] = useState<string[]>([]);
  const [isDataReady, setIsDataReady] = useState<boolean>(false);
  const counterLeftPosition = useRef<number>(0);
  const prevRemainingCount = useRef<number>(0);

  useEffect(() => {
    if (!parentRef.current) {
      return;
    }
    if (JSON.stringify(prevSelectedValues) === JSON.stringify(selectedValues)) {
      return;
    } else {
      setPrevSelectedValues(selectedValues);
    }

    const {
      visibleChips: newVisibleChips,
      hiddenChips: newHiddenChips,
      remainingCount,
      accumulatedWidth,
      isReady,
    } = calculateChipVisibility({
      selectedValues,
      parentRef: parentRef.current,
      currentColumnWidth,
    });

    if (remainingCount !== prevRemainingCount.current) {
      setRemainingCount(remainingCount);
      prevRemainingCount.current = remainingCount;
    }

    setIsDataReady(isReady);
    setVisibleChips(newVisibleChips);
    setHiddenChips(newHiddenChips);
    counterLeftPosition.current = accumulatedWidth;
  }, [selectedValues, currentColumnWidth, setRemainingCount, parentRef, prevSelectedValues]);

  return { visibleChips, hiddenChips, counterLeftPosition: counterLeftPosition.current, isDataReady };
};
