import { PuiCheckbox } from '../../../components';
import type { PuiBooleanEditProps } from '../types';
import { BooleanIcon } from './BooleanIcon';

export const PuiBooleanEdit: React.FC<PuiBooleanEditProps> = ({
  checked,
  disabled,
  color,
  sx = {},
  iconSx = {},
  displayType,
  onChange,
}) => {
  return (
    <PuiCheckbox
      checked={checked}
      disabled={disabled}
      sx={{
        '&.MuiCheckbox-root, &.Mui-disabled': { color },
        '&.Mui-checked': { color },
        '&.MuiButtonBase-root.Mui-checked .MuiSwitch-root .MuiSwitch-track': { backgroundColor: color },
        ...sx,
      }}
      icon={
        <BooleanIcon
          isChecked={false}
          displayType={displayType}
          color={color}
          sx={iconSx}
        />
      }
      checkedIcon={
        <BooleanIcon
          isChecked
          displayType={displayType}
          color={color}
          sx={iconSx}
        />
      }
      onChange={onChange}
    />
  );
};
