import type { MenuProps } from '@mui/material';
import { useEffect, useState } from 'react';

import { PuiButton, PuiFade } from '../../..';
import { validateEmail } from '../validateEmail';
import { EmailInput } from './EmailInput';
import { PopupButtonsWrapper, PopupMenu } from './StyledComponents';

export interface EmailPopupMenuProps {
  initialValue: string;
  anchorEl: HTMLElement | null;
  isOpened: boolean;
  popupMenuSx?: MenuProps['sx'];
  onCloseMenu: () => void;
  onSave: (value: string) => void;
}

export const EmailPopupMenu: React.FC<EmailPopupMenuProps> = ({
  initialValue,
  anchorEl,
  isOpened,
  popupMenuSx,
  onCloseMenu,
  onSave,
}) => {
  const [value, setValue] = useState(initialValue);
  const [validationError, setValidationError] = useState<null | string>(null);

  const handleSave = () => {
    const isValid = validateEmail(value);

    if (!isValid) {
      setValidationError('Enter a valid email address');

      return;
    }
    if (initialValue === value) {
      onCloseMenu();

      return;
    }

    setValidationError(null);
    onSave(value);
    onCloseMenu();
  };
  const handleCloseMenu = () => {
    setValue(initialValue);
    setValidationError(null);
    onCloseMenu();
  };
  const handleChange = (value: string) => setValue(value);

  useEffect(() => {
    setValue(initialValue);
  }, [initialValue]);

  return (
    <PopupMenu
      elevation={0}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'left',
      }}
      anchorEl={anchorEl}
      open={isOpened}
      sx={popupMenuSx}
      onClose={handleCloseMenu}
      TransitionComponent={PuiFade}
      data-testid='email-popup-menu'
      slotProps={{
        root: { sx: { pointerEvents: 'none' } },
        paper: { sx: { pointerEvents: 'auto' } },
      }}
    >
      <EmailInput
        value={value}
        validationError={validationError}
        styleMod='bordered'
        onChange={handleChange}
        onKeyDown={handleSave}
      />
      <PopupButtonsWrapper>
        <PuiButton
          sx={{ '&.MuiButtonBase-root': { padding: '9px 18px' } }}
          onClick={handleCloseMenu}
        >
          Cancel
        </PuiButton>
        <PuiButton
          variant='contained'
          sx={{ '&.MuiButtonBase-root': { padding: '9px 18px' } }}
          onClick={handleSave}
        >
          Save
        </PuiButton>
      </PopupButtonsWrapper>
    </PopupMenu>
  );
};
