import type { CheckboxProps } from '@mui/material';
import { useTheme } from '@mui/material';
import Checkbox from '@mui/material/Checkbox';
import React from 'react';

import { PuiIcon, PuiSvgIcon } from '../../icons';

type ExtendedCheckboxProps = CheckboxProps;

const getIconSize = (size: ExtendedCheckboxProps['size']) => {
  if (size === 'large') return 42;
  if (size === 'small') return 18;

  return 24;
};

export const PuiCheckbox: React.FC<ExtendedCheckboxProps> = ({ size, color, icon, checkedIcon, ...props }) => {
  const theme = useTheme();

  const getColor = () => {
    if (!color || color === 'default') return theme.palette.primary.main;

    return theme.palette[color]?.main || color;
  };

  const iconSize = getIconSize(size);

  return (
    <Checkbox
      disableRipple
      sx={{ color: getColor() }}
      color={color}
      size={size}
      icon={
        icon || (
          <PuiSvgIcon
            icon={PuiIcon.Checkbox}
            width={iconSize}
            height={iconSize}
          />
        )
      }
      checkedIcon={
        checkedIcon || (
          <PuiSvgIcon
            icon={PuiIcon.CheckboxChecked}
            width={iconSize}
            height={iconSize}
          />
        )
      }
      {...props}
    />
  );
};
