import type { RefObject } from 'react';
import { useLayoutEffect, useRef, useState } from 'react';

type Size = [number, number, number, number];

interface UseResizeObserverOptions {
  debounceTime?: number;
}

function debounce<T extends (...args: unknown[]) => void>(fn: T, delay: number) {
  let timer: ReturnType<typeof setTimeout> | null = null;

  return (...args: Parameters<T>): void => {
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(() => {
      fn(...args);
      timer = null;
    }, delay);
  };
}

export const useResizeObserver = (
  elementRef: RefObject<Element> | typeof window,
  options?: UseResizeObserverOptions
): Size => {
  const { debounceTime } = options ?? {};
  const [size, setSize] = useState<Size>(() => {
    if (elementRef === window) {
      return [window.innerWidth, window.innerHeight, 0, 0];
    }

    return [0, 0, 0, 0];
  });

  const resizeHandler = useRef<((entries: ResizeObserverEntry[]) => void) | null>(null);

  useLayoutEffect(() => {
    if (elementRef === window) {
      const handleResize = () => {
        setSize([window.innerWidth, window.innerHeight, 0, 0]);
      };

      resizeHandler.current = debounceTime ? debounce(() => handleResize(), debounceTime) : () => handleResize();

      const resizeListener = () => resizeHandler.current?.([]);

      window.addEventListener('resize', resizeListener);

      return () => {
        window.removeEventListener('resize', resizeListener);
        resizeHandler.current = null;
      };
    }

    const element = (elementRef as RefObject<Element>).current;
    if (!element) {
      return;
    }

    const handleResize = (entries: ResizeObserverEntry[]) => {
      const { width, height } = entries[0]?.contentRect ?? { width: 0, height: 0 };
      const { scrollWidth, scrollHeight } = entries[0]?.target ?? { scrollWidth: 0, scrollHeight: 0 };
      setSize([width, height, scrollWidth, scrollHeight]);
    };

    resizeHandler.current = debounceTime
      ? debounce((...args: unknown[]) => handleResize(args[0] as ResizeObserverEntry[]), debounceTime)
      : (entries: ResizeObserverEntry[]) => handleResize(entries);

    const resizeObserver = new ResizeObserver(entries => resizeHandler.current?.(entries));
    resizeObserver.observe(element);

    return () => {
      resizeObserver.unobserve(element);
      resizeHandler.current = null;
    };
  }, [elementRef, debounceTime]);

  return size;
};
