import PuiClickAwayListener from '@mui/material/ClickAwayListener';
import type { SelectChangeEvent } from '@mui/material/Select';
import React, { useCallback, useEffect, useRef, useState } from 'react';

import type { PopperAnchor } from '../../../';
import { PuiFade, PuiIcon, PuiInputAdornment, PuiPopper, useTheme } from '../../../';
import { POPPER_PRESET_DEFAULT } from '../../../helpers';
import { useDropdown } from '../../../hooks/useDropdown';
import { useKeydownEvent } from '../../../hooks/useKeydownEvent';
import type { StatusDropdownProps, StatusOption } from '../types';
import { StatusDisplayTypes } from '../types';
import { ClassicStatusItem } from './ClassicStatusItem';
import { FillStatusItem } from './FillStatusItem';
import { LeftIndicatorStatusItem } from './LeftIndicatorStatusItem';
import { RoundStatusItem } from './RoundStatusItem';
import {
  StatusSelect,
  StatusSelectMenuList,
  StyledClassicStatusItem,
  StyledCloseIcon,
  StyledFillStatusItem,
  StyledLeftIndicatorStatusItem,
  StyledOpenIcon,
  StyledRoundStatusItem,
} from './StyledComponents';

export const StatusDropdown: React.FC<StatusDropdownProps> = ({
  statusOptions,
  displayType,
  selectedValue,
  onValueChange,
  disabled,
  open: openProp,
  onMenuOpen,
  onMenuClose,
  className,
  placeholder = '',
  sx,
}) => {
  const theme = useTheme();

  const selectRef = useRef<HTMLDivElement>(null);
  const menuListRef = useRef<HTMLDivElement>(null);
  const [dropdownOpen, setDropdownOpen] = useState(Boolean(openProp));
  const [rectReady, setRectReady] = useState(false);
  const [virtualElement, setVirtualElement] = useState<PopperAnchor>({
    getBoundingClientRect: () => new DOMRect(),
  });

  const openDropdown = useCallback(() => {
    if (disabled || dropdownOpen) {
      return;
    }
    setDropdownOpen(true);
    onMenuOpen?.();
  }, [disabled, dropdownOpen, onMenuOpen]);

  const closeDropdown = useCallback(() => {
    if (!dropdownOpen) {
      return;
    }
    setDropdownOpen(false);
    onMenuClose?.();
  }, [dropdownOpen, onMenuClose]);

  const toggleDropdown = useCallback(() => {
    dropdownOpen ? closeDropdown() : openDropdown();
  }, [dropdownOpen, closeDropdown, openDropdown]);

  const { selectedOptionIndex, handleChange, handleReset, handleArrowNavigation } = useDropdown({
    open: dropdownOpen && rectReady,
    options: statusOptions,
    selectedValue,
    onValueChange,
    disabled,
    onMenuClose: closeDropdown,
  });

  useKeydownEvent(handleArrowNavigation);

  const type = displayType ?? StatusDisplayTypes.Fill;

  useEffect(() => {
    if (dropdownOpen && selectRef.current) {
      setRectReady(true);
      const anchorNode = selectRef.current;
      setVirtualElement({
        getBoundingClientRect: () => anchorNode.getBoundingClientRect(),

        contextElement: anchorNode,
      });
    } else {
      setRectReady(false);
    }
  }, [dropdownOpen]);

  useEffect(() => {
    if (openProp !== undefined) {
      openProp ? openDropdown() : closeDropdown();
    }
  }, [closeDropdown, openDropdown, openProp]);

  const getBackgroundColor = (type: StatusDisplayTypes) => {
    if (statusOptions?.length > 0 && selectedOptionIndex > -1 && type == StatusDisplayTypes.Fill) {
      return statusOptions[selectedOptionIndex].color;
    }

    return 'transparent';
  };

  const getColor = () => {
    if (displayType == StatusDisplayTypes.Fill && selectedOptionIndex > -1) {
      return theme.palette.common.white;
    }

    return theme.palette.grey[400];
  };

  const optionComponents = {
    [StatusDisplayTypes.LeftIndicator]: {
      item: LeftIndicatorStatusItem,
      menu: StyledLeftIndicatorStatusItem,
      menuBackground: theme.palette.common.white,
    },
    [StatusDisplayTypes.Classic]: {
      item: ClassicStatusItem,
      menu: StyledClassicStatusItem,
      menuBackground: theme.palette.background.default,
    },
    [StatusDisplayTypes.Fill]: {
      item: FillStatusItem,
      menu: StyledFillStatusItem,
      menuBackground: theme.palette.common.white,
    },
    [StatusDisplayTypes.Round]: {
      item: RoundStatusItem,
      menu: StyledRoundStatusItem,
      menuBackground: theme.palette.common.white,
    },
  };

  const { item: OptionItem, menu: OptionMenuItem, menuBackground } = optionComponents[type];

  const handleClickAway = () => closeDropdown();

  const handleContainerClick: React.MouseEventHandler = event => {
    if (disabled) return;
    if (event.button !== 0) return;
    toggleDropdown();
  };

  const endAdornment = (
    <PuiInputAdornment position='end'>
      <StyledOpenIcon
        icon={dropdownOpen ? PuiIcon.ArrowDropUp : PuiIcon.ArrowDropDown}
        stroke={getColor()}
        width='16px'
        height='16px'
        onClick={handleContainerClick}
      />
      {selectedOptionIndex !== -1 && (
        <StyledCloseIcon
          icon={PuiIcon.XClose}
          stroke={getColor()}
          width='14px'
          height='14px'
          onClick={handleReset}
        />
      )}
    </PuiInputAdornment>
  );

  const getValueFromIndex = (index: number): string => {
    const value: StatusOption | undefined = statusOptions[index];

    if (value) {
      return value.id;
    }

    return selectedValue ?? '';
  };

  return (
    <PuiClickAwayListener onClickAway={handleClickAway}>
      <>
        <StatusSelect
          onClick={handleContainerClick}
          ref={selectRef}
          endAdornment={endAdornment}
          className={`${selectedOptionIndex == -1 && !dropdownOpen ? 'StatusSelect-empty' : ''} ${className ?? ''}`}
          displayEmpty={!!placeholder}
          textcolor={getColor()}
          background={getBackgroundColor(type)}
          value={getValueFromIndex(selectedOptionIndex)}
          onChange={handleChange}
          disabled={disabled}
          sx={sx}
          open={false}
          IconComponent={() => null}
        >
          {placeholder && (
            <OptionMenuItem
              style={{ display: 'none' }}
              value=''
              key=''
              color=''
            >
              {placeholder}
            </OptionMenuItem>
          )}

          {statusOptions?.map(option => (
            <OptionMenuItem
              key={option.id}
              value={option.id}
              style={{ display: 'none' }}
              color={option.color}
            >
              <OptionItem option={option} />
            </OptionMenuItem>
          ))}
        </StatusSelect>
        <PuiPopper
          open={dropdownOpen && rectReady}
          anchorEl={rectReady ? virtualElement : selectRef.current}
          {...POPPER_PRESET_DEFAULT}
          sx={{ zIndex: theme => theme.zIndex.modal }}
        >
          {({ TransitionProps }) => (
            <PuiFade
              {...TransitionProps}
              style={{ borderRadius: 4 }}
            >
              <div
                ref={menuListRef}
                style={{
                  backgroundColor: menuBackground,
                  boxShadow: theme.shadows[2],
                  minWidth: selectRef.current?.clientWidth,
                }}
              >
                <StatusSelectMenuList>
                  {statusOptions?.map(option => {
                    return (
                      <OptionMenuItem
                        key={option.id}
                        value={option.id}
                        color={option.color}
                        onClick={() => {
                          handleChange({ target: { value: option.id } } as SelectChangeEvent<unknown>);
                          closeDropdown();
                        }}
                      >
                        <OptionItem option={option} />
                      </OptionMenuItem>
                    );
                  })}
                </StatusSelectMenuList>
              </div>
            </PuiFade>
          )}
        </PuiPopper>
      </>
    </PuiClickAwayListener>
  );
};
