import type { ChangeEvent, FC } from 'react';

import { BaseSlider } from '../common/BaseSlider';
import { Input, InputWrapper, PercentageSign, Wrapper } from '../StyledComponents';
import type { PuiSliderProps } from '../types';

export const PuiSliderEdit: FC<PuiSliderProps> = ({
  setValue,
  value,
  min = 0,
  max = 100,
  percentage = false,
  disabled = false,
  inputWrapperSx,
  inputSx,
  ...props
}) => {
  const handleSliderChange = (_: Event, newValue: number | number[]) => {
    setValue(newValue as number);
  };

  const handleInputChange = (event: ChangeEvent<HTMLInputElement>) => {
    setValue(event.target.value === '' ? 0 : minMaxValue(Number(event.target.value)));
  };

  const handleBlur = () => {
    if ((value as number) < min) {
      setValue(min);
    } else if ((value as number) > max) {
      setValue(max);
    }
  };

  const minMaxValue = (value: number) => {
    if (value < min) {
      return min;
    }
    if (value > max) {
      return max;
    } else {
      return value;
    }
  };

  return (
    <Wrapper>
      <BaseSlider
        value={value}
        onChange={handleSliderChange}
        min={min}
        max={max}
        percentage={percentage}
        disabled={disabled}
        {...props}
      />
      <InputWrapper sx={inputWrapperSx}>
        <Input
          value={Number(value).toString()}
          size='small'
          onChange={handleInputChange}
          onBlur={handleBlur}
          type='number'
          disableUnderline
          disabled={disabled}
          sx={inputSx}
          inputProps={{
            min,
            max,
            'aria-labelledby': 'input-slider',
          }}
        />
        <PercentageSign sx={{ opacity: percentage ? 1 : 0 }}>%</PercentageSign>
      </InputWrapper>
    </Wrapper>
  );
};
