export const getCompanyDisplayText = (company?: string, position?: { jobTitle: string }): string => {
  const hasCompany = company;
  const hasPosition = position?.jobTitle;

  if (hasCompany) {
    if (hasPosition) {
      return `${position.jobTitle} at ${company}`;
    }

    return company;
  }

  if (hasPosition) {
    return ` ${position.jobTitle}`;
  }

  return '';
};

export const getAddressDisplayText = (address?: {
  details: { city: string; country: string; postalCode: string; route: string };
}): string => {
  const hasAddress = address;
  const hasCity = address?.details?.city;
  const hasCountry = address?.details?.country;
  const hasPostalCode = address?.details?.postalCode;
  const hasRoute = address?.details?.route;

  if (hasAddress) {
    const parts = [];

    if (hasRoute) {
      parts.push(address.details.route);
    }
    if (hasCity) {
      parts.push(address.details.city);
    }
    if (hasCountry) {
      parts.push(address.details.country);
    }
    if (hasPostalCode) {
      parts.push(address.details.postalCode);
    }

    return parts.join(', ');
  }

  return '';
};
