import type { FC } from 'react';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';

import { PuiBox, PuiMenuItem, PuiTypography } from '../../../components';
import { PuiIcon, PuiSvgIcon } from '../../../icons';
import { BaseTagCell } from '../common/BaseTagCell';
import type { TagCellEditProps, TagItem } from '../types';
import { containsText } from '../utils';
import SelectedOptions from './SelectedOptions';
import {
  ActionButton,
  ActionButtonsContainer,
  ActionButtonsWrapper,
  ChipMenuItem,
  CreateNewButton,
  CreateNewButtonWrapper,
  SearchInputWrapper,
  StyledInput,
  StyledPopper,
} from './StyledComponents';

export const PuiTagCellEdit: FC<TagCellEditProps> = ({
  displayType,
  options,
  selectedValues,
  currentColumnWidth,
  disabled,
  tagCellSx,
  optionSx,
  chipSx = {},
  onDelete,
  onValueChange,
  onCreateTag,
  onSave,
  onCancel,
  shouldOpenInitially = false,
  onDropdownMenuOpen,
  onDropdownMenuClose,
  placeholder,
  popperSx,
}) => {
  const [searchText, setSearchText] = useState('');
  const [isTooltipOpen, setIsTooltipOpen] = useState(false);
  const [remainingCount, setRemainingCount] = useState(0);
  const [countClicksOnTagCell, setCountClicksOnTagCell] = useState(0);

  const boxRef = useRef<null | HTMLElement>(null);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const optionsTitles = options?.map(({ title }) => title);
  const selectedTitles = selectedValues.map(({ title }) => title);

  const open = shouldOpenInitially || Boolean(anchorEl);
  const MINIMUM_CLICKS_TO_OPEN_DROPDOWN = shouldOpenInitially ? 1 : 2;

  useEffect(() => {
    if (boxRef.current) {
      setAnchorEl(boxRef.current);
    }
  }, []);

  const onTooltipClose = useCallback(() => {
    setIsTooltipOpen(false);
  }, []);

  const onTooltipOpen = useCallback(() => {
    if (!open && remainingCount > 0) {
      setIsTooltipOpen(true);
    }
  }, [open, remainingCount]);

  const dropdownValues = useMemo(() => {
    return options
      ?.filter(option => !selectedValues.find(selected => selected.id === option.id))
      .filter(option => containsText(option.title, searchText));
  }, [options, searchText, selectedValues]);

  const onTagCellOpen = useCallback(() => {
    if (disabled) {
      return;
    }
    onTooltipClose();
    setSearchText('');

    if (countClicksOnTagCell < MINIMUM_CLICKS_TO_OPEN_DROPDOWN) {
      setCountClicksOnTagCell(countClicksOnTagCell + 1);
    }
    setAnchorEl(boxRef.current);
    if (open && onDropdownMenuOpen) {
      onDropdownMenuOpen();
    }
  }, [MINIMUM_CLICKS_TO_OPEN_DROPDOWN, countClicksOnTagCell, disabled, onDropdownMenuOpen, onTooltipClose, open]);

  const onDropdownClose = useCallback(() => {
    setAnchorEl(null);
    setCountClicksOnTagCell(0);
    if (shouldOpenInitially) {
      return;
    }
    if (onDropdownMenuClose) {
      onDropdownMenuClose();
    }
  }, [onDropdownMenuClose, shouldOpenInitially]);

  const handleDelete = useCallback(
    (value: string) => {
      onDelete?.(value);
    },
    [onDelete]
  );

  const onCreateNewOption = useCallback(() => {
    if (
      selectedValues.map(({ title }) => title).includes(searchText) ||
      options?.map(({ title }) => title).includes(searchText)
    ) {
      return;
    }
    onCreateTag?.(searchText);
    setSearchText('');
  }, [selectedValues, searchText, options, onCreateTag]);

  const onSelectValue = useCallback(
    (value: TagItem) => {
      onValueChange?.(value);
    },
    [onValueChange]
  );

  const onSaveSelectedValues = useCallback(() => {
    onDropdownClose();
    onSave?.();
  }, [onDropdownClose, onSave]);

  const onCancelSelectedValues = useCallback(() => {
    onDropdownClose();
    onCancel?.();
  }, [onCancel, onDropdownClose]);

  const handleKeyDown = useCallback((e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key !== 'Escape') {
      e.stopPropagation();
    }
  }, []);

  return (
    <PuiBox>
      <BaseTagCell
        selectedValues={selectedValues}
        disabled={disabled}
        tagCellSx={tagCellSx}
        isTooltipOpen={isTooltipOpen}
        onTooltipClose={onTooltipClose}
        onTooltipOpen={onTooltipOpen}
        onClick={onTagCellOpen}
        className={open ? 'open' : ''}
        ref={boxRef}
      >
        <SelectedOptions
          selectedValues={selectedValues}
          displayType={displayType}
          showCircleButton={true}
          isDropdownOpen={open}
          currentColumnWidth={currentColumnWidth}
          optionSx={optionSx}
          chipSx={chipSx}
          remainingCount={remainingCount}
          setRemainingCount={setRemainingCount}
          onDelete={handleDelete}
          placeholder={placeholder}
        />
      </BaseTagCell>

      <StyledPopper
        open={open && !!anchorEl}
        placement='bottom-start'
        anchorEl={anchorEl}
        sx={popperSx}
        modifiers={[
          {
            name: 'offset',
            options: {
              offset: [0, 2],
            },
          },
        ]}
        style={{
          display: countClicksOnTagCell >= MINIMUM_CLICKS_TO_OPEN_DROPDOWN ? 'flex' : 'none',
        }}
      >
        <SearchInputWrapper>
          <StyledInput
            size='small'
            autoFocus
            placeholder='Search tags or add new'
            fullWidth
            value={searchText}
            onChange={e => setSearchText(e.target.value)}
            onKeyDown={handleKeyDown}
          />
        </SearchInputWrapper>
        {dropdownValues?.map(item => (
          <PuiMenuItem
            key={item.id}
            value={item.title}
            style={{ padding: 0 }}
          >
            <ChipMenuItem
              label={item.title}
              displayType={displayType}
              sx={{ ...chipSx }}
              onClick={() => onSelectValue(item)}
            />
          </PuiMenuItem>
        ))}
        {searchText && !optionsTitles?.includes(searchText) && !selectedTitles.includes(searchText) && (
          <CreateNewButtonWrapper>
            <CreateNewButton
              variant='text'
              size='small'
              color='primary'
              onClick={onCreateNewOption}
            >
              <PuiSvgIcon
                icon={PuiIcon.Plus}
                width={12}
                height={12}
                strokeWidth={4}
              />
              <PuiTypography>Create new</PuiTypography>
            </CreateNewButton>
          </CreateNewButtonWrapper>
        )}

        <ActionButtonsWrapper>
          <ActionButtonsContainer>
            <ActionButton
              variant='text'
              size='small'
              color='primary'
              onClick={onCancelSelectedValues}
            >
              Cancel
            </ActionButton>
            <ActionButton
              variant='contained'
              size='small'
              color='info'
              onClick={onSaveSelectedValues}
            >
              Save
            </ActionButton>
          </ActionButtonsContainer>
        </ActionButtonsWrapper>
      </StyledPopper>
    </PuiBox>
  );
};
