import { ComponentMode } from '../../types';
import { PuiTextEdit } from './edit/TextEdit';
import type { PuiTextProps } from './types';
import { PuiTextView } from './view/TextView';

export const PuiText: React.FC<PuiTextProps> = ({ value, mode = ComponentMode.Edit, ...restProps }) => {
  if (mode === ComponentMode.Edit) {
    return (
      <PuiTextEdit
        value={value}
        {...restProps}
      />
    );
  }
  if (mode === ComponentMode.View) {
    return <PuiTextView value={value} />;
  }

  return null;
};
