import type { PuiContact } from '../../..';
import { getDomainName, isoDateParts, PuiStack } from '../../..';
import { GreenPhone, LocationMap, MailIOS, OfficeBuilding } from '../../icons';
import { PuiBox, PuiDivider, PuiTypography } from '..';
import { PuiContactCardHeader } from './ContactCardHeader';
import { PuiContactInfoItem } from './ContactInfoItem';
import { PuiContactSocialMediaItem } from './ContactSocialMediaItem';
import { SocialIconWrapper, StyledStatusIndicator } from './StyledComponents';
import type { ContactCardActions, StatusOptions } from './types';
import { InfoItemType } from './types';
import { getAddressDisplayText, getCompanyDisplayText } from './utils';

export interface PuiContactCardProps {
  contact: PuiContact;
  actions: ContactCardActions;
  coverImage: { position: { y: number }; url: string; containerDimensions?: { width: number; height: number } };
  contactStatus?: { text: string; status: StatusOptions };
  showCoverButton?: boolean;
}

export const PuiContactCard: React.FC<PuiContactCardProps> = props => {
  const { name, email, birthday, address, company, phones, socialMedia, additionalEmails, nameDay, position } =
    props.contact;

  const birthdayParts = isoDateParts(birthday ? new Date(birthday) : undefined);
  const nameDayParts = isoDateParts(nameDay ? new Date(nameDay) : undefined);

  const birthdayDay = birthdayParts.find(part => part.type === 'weekday')?.value ?? '';
  const birthdayDate = birthdayParts.find(part => part.type === 'day')?.value ?? '';
  const birthdayMonth = birthdayParts.find(part => part.type === 'month')?.value ?? '';

  const nameDayDay = nameDayParts.find(part => part.type === 'weekday')?.value ?? '';
  const nameDayDate = nameDayParts.find(part => part.type === 'day')?.value ?? '';
  const nameDayMonth = nameDayParts.find(part => part.type === 'month')?.value ?? '';

  return (
    <PuiBox>
      <PuiContactCardHeader
        contact={props.contact}
        coverImage={props.coverImage}
        actions={props.actions}
        showCoverButton={props.showCoverButton}
      />
      <PuiBox sx={{ padding: '0 32px' }}>
        <PuiBox sx={{ marginBottom: '24px' }}>
          <PuiTypography
            variant='h5'
            sx={{ marginBottom: '8px' }}
          >
            {name}
          </PuiTypography>
          {props.contactStatus && (
            <PuiStack sx={{ flexDirection: 'row', alignItems: 'center', gap: '8px' }}>
              <StyledStatusIndicator className={props.contactStatus.status} />
              <PuiTypography
                variant='body-sm-medium'
                color='grey.400'
              >
                {props.contactStatus.text}
              </PuiTypography>
            </PuiStack>
          )}
        </PuiBox>
        <PuiBox sx={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
          <PuiContactInfoItem
            title='Email'
            text={email}
            icon={<MailIOS />}
            type={InfoItemType.Email}
          />
          {Array.isArray(additionalEmails) && additionalEmails.length > 0
            ? additionalEmails.map(item => (
                <PuiContactInfoItem
                  key={item.id}
                  title={`${item.label} email`}
                  text={item.email}
                  icon={<MailIOS />}
                  type={InfoItemType.Phone}
                />
              ))
            : null}
          {Array.isArray(phones) && phones.length > 0
            ? phones.map(item => (
                <PuiContactInfoItem
                  key={item.id}
                  title={`${item.label} phone`}
                  text={item.phone}
                  icon={<GreenPhone />}
                  type={InfoItemType.Phone}
                />
              ))
            : null}
          {getAddressDisplayText(address) && (
            <PuiContactInfoItem
              title='Address'
              text={getAddressDisplayText(address)}
              icon={<LocationMap />}
            />
          )}
          {getCompanyDisplayText(company, position) && (
            <PuiContactInfoItem
              title='Company'
              text={getCompanyDisplayText(company, position)}
              icon={<OfficeBuilding />}
            />
          )}
          {birthdayMonth && (
            <PuiContactInfoItem
              title='Birthday date'
              text={`${birthdayDate} ${birthdayMonth}`}
              icon={
                <SocialIconWrapper className='date'>
                  <PuiBox
                    component='span'
                    sx={{ fontSize: '8px', color: '#FF3B30', textTransform: 'uppercase' }}
                  >
                    {birthdayDay}
                  </PuiBox>
                  <PuiBox
                    component='span'
                    sx={{ fontSize: '24px', lineHeight: 1, fontWeight: 300 }}
                  >
                    {birthdayDate}
                  </PuiBox>
                </SocialIconWrapper>
              }
            />
          )}
          {nameDayMonth && (
            <PuiContactInfoItem
              title='Name day'
              text={`${nameDayDate} ${nameDayMonth}`}
              icon={
                <SocialIconWrapper className='date'>
                  <PuiBox
                    component='span'
                    sx={{ fontSize: '8px', color: '#FF3B30', textTransform: 'uppercase' }}
                  >
                    {nameDayDay}
                  </PuiBox>
                  <PuiBox
                    component='span'
                    sx={{ fontSize: '24px', lineHeight: 1, fontWeight: 300 }}
                  >
                    {nameDayDate}
                  </PuiBox>
                </SocialIconWrapper>
              }
            />
          )}
          <PuiDivider />
          {socialMedia?.map((item, index) => (
            <PuiContactSocialMediaItem
              key={item + index}
              link={item}
              domain={getDomainName(item)}
            />
          ))}
        </PuiBox>
      </PuiBox>
    </PuiBox>
  );
};
