function isValidImage(url: string): Promise<boolean> {
  return new Promise(resolve => {
    const img = new Image();
    img.src = url;

    img.onload = () => resolve(true);
    img.onerror = () => resolve(false);
  });
}

export function getFaviconUrl(siteURL: string): Promise<string | null> {
  try {
    const url = new URL(siteURL);
    const faviconUrl = `${url.origin}/favicon.ico`;

    return isValidImage(faviconUrl).then(isValid => {
      return isValid ? faviconUrl : null;
    });
  } catch (error) {
    console.error('Invalid URL:', siteURL);

    return Promise.resolve(null);
  }
}

export const validateUrl = (url: string) => {
  const urlPattern = new RegExp('^(https?://)?' + '([a-zA-Z0-9-]+\\.)+' + '[a-zA-Z]{2,}' + '(/[^\\s]*)?$');

  return urlPattern.test(url);
};
