import { ComponentMode, PuiLinkEdit, PuiLinkView } from '../../..';
import type { PuiLinkFieldProps } from './types';

export const PuiLinkField: React.FC<PuiLinkFieldProps> = ({
  link,
  displayType,
  isActive,
  mode = ComponentMode.Edit,
  displayDefaultIcon,
  sx,
  ...restProps
}) => {
  if (mode === ComponentMode.View) {
    return (
      <PuiLinkView
        link={link}
        displayType={displayType}
        isActive={isActive}
        displayDefaultIcon={displayDefaultIcon}
        sx={sx}
      />
    );
  }
  if (mode === ComponentMode.Edit) {
    return (
      <PuiLinkEdit
        value={link.value}
        displayType={displayType}
        {...restProps}
      />
    );
  }

  return null;
};
