import { useTheme } from '@mui/material';

import type { PuiCheckboxViewProps } from '../types';
import { BooleanDisplayConfigType } from '../types';
import { PuiBooleanDefaultView } from './BooleanDefaultView';
import { PuiSwitchView } from './BooleanSwitchView';

export const PuiCheckboxView: React.FC<PuiCheckboxViewProps> = ({ checked, color = '#3398DB', displayType, sx }) => {
  const theme = useTheme();

  return (
    <>
      {displayType === BooleanDisplayConfigType.Switch ? (
        <PuiSwitchView
          color={checked ? color : theme.palette.grey[50]}
          checked={checked}
          sx={sx}
        />
      ) : (
        <PuiBooleanDefaultView
          checked={checked}
          color={color}
          displayType={displayType}
          sx={sx}
        />
      )}
    </>
  );
};
