import type { FC } from 'react';

import { ComponentMode } from '../../types';
import { PuiTagCellEdit } from './edit/TagCellEdit';
import type { TagCellEditProps } from './types';
import { PuiTagCellView } from './view/TagCellView';

type TagCellProps = TagCellEditProps & { mode?: ComponentMode };

export const PuiTagCell: FC<TagCellProps> = ({
  displayType,
  selectedValues,
  currentColumnWidth,
  disabled = false,
  tagCellSx,
  optionSx,
  chipSx = {},
  shouldOpenInitially = false,
  mode = ComponentMode.Edit,
  options = [],
  ...restProps
}) => {
  if (mode === ComponentMode.View) {
    return (
      <PuiTagCellView
        displayType={displayType}
        selectedValues={selectedValues}
        placeholder={restProps.placeholder}
        currentColumnWidth={currentColumnWidth}
        disabled={disabled}
        tagCellSx={tagCellSx}
        optionSx={optionSx}
        chipSx={chipSx}
      />
    );
  }

  if (mode === ComponentMode.Edit) {
    return (
      <PuiTagCellEdit
        displayType={displayType}
        selectedValues={selectedValues}
        currentColumnWidth={currentColumnWidth}
        disabled={disabled}
        tagCellSx={tagCellSx}
        optionSx={optionSx}
        chipSx={chipSx}
        shouldOpenInitially={shouldOpenInitially}
        options={options}
        {...restProps}
      />
    );
  }

  return null;
};
