import { useTheme } from '@mui/material';
import { includesNormalized } from 'piche.npm.core';
import { useCallback, useEffect, useMemo, useState } from 'react';

import type { PuiUser } from '../../..';
import { PuiInputAdornment, PuiList } from '../../..';
import { PuiIcon, PuiSvgIcon } from '../../icons';
import { PuiPersonsListItem } from './PersonsListItem';
import { StyledSearchInput, StyledUserOptionsContainer } from './StyledComponents';

export interface PuiPersonsListProps {
  users: PuiUser[];
  multiple?: boolean;
  placeholder?: string;
  anchorEl?: HTMLElement;
  onChange: (ids: string[]) => void;
  selectedIds?: string[];
  onClick?: (user: PuiUser) => void;
}

export const PuiPersonsList: React.FC<PuiPersonsListProps> = ({
  users,
  multiple,
  placeholder = 'Search people',
  onChange,
  selectedIds = [],
}) => {
  const [searchValue, setSearchValue] = useState<string>('');
  const [selectedUsers, setSelectedUsers] = useState<PuiUser[]>(
    selectedIds.map(id => users.find(user => user.id === id)) as PuiUser[]
  );
  const [highlightedIndex, setHighlightedIndex] = useState<number>(0);

  const theme = useTheme();
  const sortedUsers = useMemo(() => {
    const filteredUsers = searchValue
      ? users.filter(user => includesNormalized(user.email, searchValue) || includesNormalized(user.name, searchValue))
      : users;
    const selectedIdsSet = new Set(selectedUsers.map(user => user.id));
    const selectedPersonas = filteredUsers.filter(user => selectedIdsSet.has(user.id));
    const unselectedPersonas = filteredUsers.filter(user => !selectedIdsSet.has(user.id));

    return [...selectedPersonas, ...unselectedPersonas];
  }, [searchValue, users, selectedUsers]);

  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchValue(event.target.value);
    setHighlightedIndex(0);
  };

  const handleSingleSelect = (user: PuiUser) => {
    const newSelected = selectedUsers[0]?.id === user.id ? [] : [user];
    setSelectedUsers(newSelected);
    onChange(newSelected.length ? [user.id] : []);
  };

  const handleMultiSelect = (user: PuiUser) => {
    const newSelected = selectedUsers.some(selectedUser => selectedUser.id === user.id)
      ? selectedUsers.filter(selectedUser => selectedUser.id !== user.id)
      : [...selectedUsers, user];
    setSelectedUsers(newSelected);
    onChange(newSelected.map(user => user.id));
  };

  const handleSelect = multiple ? handleMultiSelect : handleSingleSelect;

  const handleArrowNavigation = useCallback(
    (event: KeyboardEvent) => {
      if (!sortedUsers.length) {
        return;
      }

      const eventsKeys = ['ArrowDown', 'ArrowUp', 'Enter'];
      if (eventsKeys.includes(event.key)) {
        event.preventDefault();

        if (event.key === 'ArrowDown') {
          setHighlightedIndex(prev => (prev + 1) % sortedUsers.length);
        } else if (event.key === 'ArrowUp') {
          setHighlightedIndex(prev => (prev - 1 + sortedUsers.length) % sortedUsers.length);
        } else if (event.key === 'Enter') {
          event.stopPropagation();
          handleSelect(sortedUsers[highlightedIndex]);
        }
      }
    },
    [highlightedIndex, sortedUsers, handleSelect]
  );

  useEffect(() => {
    document.addEventListener('keydown', handleArrowNavigation);

    return () => {
      document.removeEventListener('keydown', handleArrowNavigation);
    };
  }, [handleArrowNavigation]);

  useEffect(() => {
    const elementId = `user-option-${highlightedIndex}`;
    const el = document.getElementById(elementId);
    if (el) {
      el.scrollIntoView({ behavior: 'smooth', block: 'nearest' });
    }
  }, [highlightedIndex]);

  return (
    <PuiList sx={{ padding: '0' }}>
      <StyledSearchInput
        autoFocus
        fullWidth={false}
        value={searchValue}
        onChange={handleSearchChange}
        startAdornment={
          <PuiInputAdornment position='start'>
            <PuiSvgIcon
              icon={PuiIcon.Search}
              width={20}
              height={20}
              stroke={theme.palette.grey[200]}
            />
          </PuiInputAdornment>
        }
        placeholder={placeholder}
      />

      <StyledUserOptionsContainer>
        {sortedUsers.map((user, index) => {
          const isHighlighted = index === highlightedIndex;

          return (
            <PuiPersonsListItem
              key={user.id}
              user={user}
              id={`user-option-${index}`}
              isHighlighted={isHighlighted}
              isSelected={selectedUsers.some(selectedUser => selectedUser.id === user.id)}
              onClick={() => handleSelect(user)}
            />
          );
        })}
      </StyledUserOptionsContainer>
    </PuiList>
  );
};
