import { ComponentMode } from '../../types';
import { PuiCurrencyEdit } from './edit/CurrencyEdit';
import type { PuiCurrencyProps } from './types';
import { PuiCurrencyView } from './view/CurrencyView';

export const PuiCurrency: React.FC<PuiCurrencyProps> = ({
  value,
  defaultCurrency,
  mode = ComponentMode.Edit,
  disabled = false,
  ...restProps
}) => {
  if (mode === ComponentMode.Edit) {
    return (
      <PuiCurrencyEdit
        value={value}
        defaultCurrency={defaultCurrency}
        disabled={disabled}
        {...restProps}
      />
    );
  }

  if (mode === ComponentMode.View) {
    return (
      <PuiCurrencyView
        value={value}
        defaultCurrency={defaultCurrency}
      />
    );
  }

  return null;
};
