import type { ButtonProps } from '@mui/material';

export const getProgressSize = (size?: ButtonProps['size']): number => {
  switch (size) {
    case 'xxsmall':
      return 12;
    case 'xsmall':
    case 'small':
      return 16;
    case 'medium':
    case 'large':
      return 24;
    default:
      return 24;
  }
};

export interface IExtraStyle {
  button?: Record<string, string | number>;
  childrenWrap?: Record<string, string | number>;
  progress?: Record<string, string | number>;
}

export const getLoadingButtonStyles = (loading: boolean | undefined, variant: ButtonProps['variant']): IExtraStyle => {
  if (loading) {
    return {
      button: { pointerEvents: 'none' },
      childrenWrap: { opacity: 0, position: 'relative', display: 'flex' },
      progress: { color: variant === 'contained' ? '#fff' : 'primary', position: 'absolute' },
    };
  }

  return {};
};
