import type { BoxProps, SelectProps } from '@mui/material';

import type { ComponentMode } from '../..';

export interface StatusOption {
  id: string;
  label: string;
  color: string;
  completed?: boolean;
}

export enum StatusDisplayTypes {
  Fill = 'fill',
  LeftIndicator = 'left-indicator',
  Classic = 'classic',
  Round = 'round',
}

export interface StatusItemProps {
  option: StatusOption;
  viewMode?: boolean;
}

export interface PuiStatusProps {
  value: string;
  displayType: StatusDisplayTypes;
  options: StatusOption[];
  disabled?: boolean;
  onHoverViewActions?: boolean;
  onClearValue?: () => void;
  manualCellEdit?: () => void;
  handleChanges: (value: string) => void;
  mode?: ComponentMode;
  className?: string;
  onMenuOpen?: () => void;
  onMenuClose?: () => void;
  open?: boolean;
  placeholder?: string;
  sx?: BoxProps['sx'] | SelectProps['sx'];
}

export interface StatusViewProps {
  statusOptions: StatusOption[];
  displayType?: StatusDisplayTypes;
  selectedValue?: string;
  isLocked?: boolean;
  onHoverViewActions?: boolean;
  sx?: BoxProps['sx'] | SelectProps['sx'];
  onClearValue?: () => void;
  manualCellEdit?: () => void;
}

export interface PuiStatusEditProps {
  value: string;
  displayType: StatusDisplayTypes;
  options: StatusOption[];
  disabled: boolean;
  open?: boolean;
  handleChanges: (value: string) => void;
  className?: string;
  onMenuOpen?: () => void;
  onMenuClose?: () => void;
  placeholder?: string;
  sx?: BoxProps['sx'] | SelectProps['sx'];
}

export interface StatusDropdownProps {
  statusOptions: StatusOption[];
  displayType?: StatusDisplayTypes;
  selectedValue?: string;
  onValueChange: (value: string) => void;
  disabled: boolean;
  open?: boolean;
  onMenuOpen?: () => void;
  onMenuClose?: () => void;
  className?: string;
  placeholder?: string;
  sx?: BoxProps['sx'] | SelectProps['sx'];
}

export interface PopperAnchor {
  getBoundingClientRect: () => DOMRect;
  contextElement?: Element;
}
