import { useTheme } from '@mui/material';

import { PuiSvgIcon } from '../../../icons';
import { IconByDisplayType } from '../edit/BooleanIcon';
import type { PuiBooleanDefaultViewProps } from '../types';
import { StyledViewBooleanContainer, StyledViewBooleanEmpty, StyledViewCheckedIconContainer } from './StyledComponents';

export const PuiBooleanDefaultView: React.FC<PuiBooleanDefaultViewProps> = ({ checked, color, displayType, sx }) => {
  const theme = useTheme();
  const fontColor = theme.palette.common.white;
  const emptyFontColor = theme.palette.grey[200];
  const emptyBorderColor = theme.palette.grey[100];

  return (
    <StyledViewBooleanContainer sx={sx}>
      {checked ? (
        <StyledViewCheckedIconContainer color={color}>
          <PuiSvgIcon
            icon={IconByDisplayType[displayType]}
            width={16}
            height={16}
            stroke={fontColor}
          />
        </StyledViewCheckedIconContainer>
      ) : (
        <StyledViewBooleanEmpty color={emptyBorderColor}>
          <PuiSvgIcon
            icon={IconByDisplayType[displayType]}
            width={16}
            height={16}
            stroke={emptyFontColor}
          />
        </StyledViewBooleanEmpty>
      )}
    </StyledViewBooleanContainer>
  );
};
