import React from 'react';

import type { StatusItemProps } from '../types';
import { StyledFillStatusItemContainer, StyledOptionTypography } from './StyledComponents';

export const FillStatusItem: React.FC<StatusItemProps> = ({ option }) => {
  return (
    <StyledFillStatusItemContainer color={option.color}>
      <StyledOptionTypography className='fill'>{option.label}</StyledOptionTypography>
    </StyledFillStatusItemContainer>
  );
};
