import { ComponentMode } from '../../types';
import { PuiBooleanEdit } from './edit/BooleanEdit';
import type { PuiBooleanProps } from './types';
import { PuiCheckboxView } from './view/BooleanView';

export const PuiBoolean: React.FC<PuiBooleanProps> = ({
  checked,
  disabled = false,
  color,
  sx = {},
  iconSx = {},
  displayType,
  onChange,
  mode = ComponentMode.Edit,
}) => {
  if (mode === ComponentMode.View) {
    return (
      <PuiCheckboxView
        color={color}
        displayType={displayType}
        checked={checked}
        sx={sx}
        iconSx={iconSx}
      />
    );
  }
  if (mode === ComponentMode.Edit) {
    return (
      <PuiBooleanEdit
        checked={checked}
        disabled={disabled}
        color={color}
        displayType={displayType}
        onChange={onChange}
        sx={sx}
        iconSx={iconSx}
      />
    );
  }

  return null;
};
