import { ComponentMode } from '../../types';
import { PuiPhoneEdit } from './edit/PhoneEdit';
import type { PuiPhoneProps } from './types';
import { PuiPhoneView } from './view/PhoneView';

export const PuiPhone: React.FC<PuiPhoneProps> = ({
  value,
  values,
  defaultCountry,
  mode = ComponentMode.Edit,
  ...restProps
}) => {
  if (mode === ComponentMode.Edit) {
    return (
      <PuiPhoneEdit
        value={value}
        values={values}
        defaultCountry={defaultCountry}
        {...restProps}
      />
    );
  }
  if (mode === ComponentMode.View) {
    return (
      <PuiPhoneView
        value={value}
        defaultCountry={defaultCountry}
        restValues={values?.length > 1 ? values.slice(1) : []}
      />
    );
  }

  return null;
};
