import React, { useRef } from 'react';

import { PuiFade, PuiIcon, PuiInputAdornment, useTheme } from '../../..';
import { useDropdown } from '../../../hooks/useDropdown';
import { useKeydownEvent } from '../../../hooks/useKeydownEvent';
import { getSingleSelectOptionComponent } from '../getSingleSelectOptionComponent';
import type { SingleSelectDisplayTypes, SingleSelectOption } from '../types';
import {
  SingleSelect,
  SingleSelectMenuList,
  StyledCloseIcon,
  StyledDropdownMenu,
  StyledOpenIcon,
} from './StyledComponents';

export interface SingleSelectDropdownProps {
  singleSelectOptions: SingleSelectOption[];
  displayType?: SingleSelectDisplayTypes;
  selectedValue?: string;
  onValueChange: (value: string) => void;
  disabled?: boolean;
  open?: boolean;
  onMenuOpen?: () => void;
  onMenuClose?: () => void;
  className?: string;
  placeholder?: string;
}

export const SingleSelectDropdown: React.FC<SingleSelectDropdownProps> = ({
  singleSelectOptions,
  displayType,
  selectedValue,
  onValueChange,
  disabled,
  open = false,
  onMenuOpen,
  onMenuClose,
  className,
  placeholder = '',
}) => {
  const theme = useTheme();
  const { selectedOptionIndex, handleChange, handleReset, handleArrowNavigation } = useDropdown({
    open,
    options: singleSelectOptions,
    selectedValue,
    onValueChange,
    disabled: disabled ?? false,
    onMenuClose,
  });

  useKeydownEvent(handleArrowNavigation);

  const menuListRef = useRef(null);
  const OptionItem = getSingleSelectOptionComponent(displayType);

  const selectIcon = () => {
    return <></>;
  };

  const endAdornment = (
    <PuiInputAdornment position='end'>
      <StyledOpenIcon
        icon={open ? PuiIcon.ArrowDropUp : PuiIcon.ArrowDropDown}
        stroke={theme.palette.grey[400]}
        width='16px'
        height='16px'
      />
      {selectedOptionIndex !== -1 && (
        <StyledCloseIcon
          icon={PuiIcon.XClose}
          stroke={theme.palette.grey[400]}
          width='14px'
          height='14px'
          onClick={handleReset}
        />
      )}
    </PuiInputAdornment>
  );

  return (
    <SingleSelect
      className={`${selectedOptionIndex == -1 && !open ? 'SingleSelect-empty' : ''} ${className ?? ''}`}
      IconComponent={selectIcon}
      displayEmpty={!!placeholder}
      MenuProps={{
        autoFocus: false,
        disableAutoFocusItem: true,
        disableEnforceFocus: true,
        disableAutoFocus: true,
        transformOrigin: {
          vertical: 'top',
          horizontal: 'left',
        },
        slotProps: {
          root: { sx: { pointerEvents: 'none' } },
          paper: { sx: { pointerEvents: 'auto' } },
        },
        anchorOrigin: {
          vertical: 'bottom',
          horizontal: 'left',
        },
        MenuListProps: {
          component: SingleSelectMenuList,
        },
        PaperProps: {
          ref: menuListRef,
          style: {
            marginTop: 4,
            backgroundColor: theme.palette.common.white,
          },
        },
        TransitionComponent: PuiFade,
      }}
      endAdornment={endAdornment}
      textcolor=''
      value={selectedOptionIndex > -1 ? selectedOptionIndex.toString() : ''}
      onChange={handleChange}
      open={open}
      onClick={() => {
        if (open) {
          onMenuClose?.();

          return;
        }

        if (!disabled) {
          onMenuOpen?.();
        }
      }}
    >
      {placeholder && (
        <StyledDropdownMenu
          style={{ display: 'none' }}
          value=''
        >
          {placeholder}
        </StyledDropdownMenu>
      )}
      {singleSelectOptions?.map((option, index) => (
        <StyledDropdownMenu
          value={index}
          key={option.id}
        >
          <OptionItem option={option} />
        </StyledDropdownMenu>
      ))}
    </SingleSelect>
  );
};
