import type { FC } from 'react';

import { useTheme } from '../../hooks';
import type { CustomIconProps } from './types';

export const OutOfPlace: FC<CustomIconProps> = ({ width = 12, height = 12 }) => {
  const theme = useTheme();
  const lightColor = theme.palette.background.default;
  const warningColor = theme.palette.warning.main;

  return (
    <svg
      width={width}
      height={height}
      viewBox='0 0 12 12'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
    >
      <circle
        cx='6'
        cy='6'
        r='5.5'
        fill={warningColor}
        stroke='white'
      />
      <path
        d='M6 4V6L7.33333 6.66667'
        stroke={lightColor}
        strokeLinecap='round'
        strokeLinejoin='round'
      />
    </svg>
  );
};
