import type { BoxProps } from '@mui/material';

import type { ComponentMode } from '../../types';

export enum LinkFieldDisplayTypes {
  LinkOnly = 'link-only',
  LinkWithIcon = 'link-with-icon',
  IconOnly = 'icon-only',
}

export interface LinkItem {
  href: string;
  value: string;
}

export interface PuiLinkFieldEditProps {
  link: LinkItem;
  displayType: LinkFieldDisplayTypes;
  isActive?: boolean;
  onChange: (value: string) => void;
  setValidationError?: (error: string | null) => void;
  placeholder?: string;
  showError?: boolean;
  autoFocus?: boolean;
}

export interface PuiLinkFieldProps extends PuiLinkFieldEditProps {
  mode?: ComponentMode;
  displayDefaultIcon?: boolean;
  sx?: BoxProps['sx'];
}

export interface PuiLinkBasicProps {
  link: LinkItem;
  displayType: LinkFieldDisplayTypes;
  isActive?: boolean;
  displayDefaultIcon?: boolean;
  sx?: BoxProps['sx'];
}

export interface PuiLinkEditProps {
  value: string;
  displayType: LinkFieldDisplayTypes;
  onChange?: (value: string) => void;
  placeholder?: string;
  setValidationError?: (error: string | null) => void;
  showError?: boolean;
  autoFocus?: boolean;
}
