import type { BoxProps, ChipProps } from '@mui/material';
import { useRef } from 'react';

import { useTheme } from '../../../hooks';
import { PuiIcon, PuiSvgIcon } from '../../../icons';
import { RemainingCountChip } from '../edit/StyledComponents';
import { useChipVisibility } from '../hooks/ChipVisibility';
import type { DisplayType } from '../types';
import { StyledPlaceholder, StyledSelectedOptions } from './StyledComponents';

export interface BaseSelectedOptionsProps {
  selectedValues: string[];
  displayType: DisplayType;
  showCircleButton: boolean;
  currentColumnWidth: number;
  optionSx?: BoxProps['sx'];
  chipSx?: ChipProps['sx'];
  remainingCount: number;
  setRemainingCount: (remainingCount: number) => void;
  renderChips: (params: { visibleChips: string[]; hiddenChips: string[]; isReady: boolean }) => React.ReactNode;
  placeholder?: string;
  hideTransparentItems?: boolean;
}

export const BaseSelectedOptions: React.FC<BaseSelectedOptionsProps> = ({
  selectedValues,
  showCircleButton,
  currentColumnWidth,
  optionSx,
  setRemainingCount,
  renderChips,
  placeholder = '',
  remainingCount,
  displayType,
  hideTransparentItems,
}) => {
  const parentRef = useRef<HTMLDivElement>(null);
  const theme = useTheme();

  const { visibleChips, hiddenChips, counterLeftPosition, isDataReady } = useChipVisibility({
    selectedValues,
    parentRef,
    currentColumnWidth,
    setRemainingCount,
  });

  return (
    <StyledSelectedOptions
      ref={parentRef}
      sx={optionSx}
    >
      {showCircleButton && (
        <PuiSvgIcon
          className='circle-button'
          width={20}
          height={20}
          icon={PuiIcon.PlusCircle}
          stroke={theme.palette.primary.main}
        />
      )}
      {selectedValues.length > 0 ? (
        <>
          {renderChips({ visibleChips, hiddenChips, isReady: isDataReady })}
          {hideTransparentItems && (
            <RemainingCountChip
              key='remaining-counter'
              label={`+${remainingCount}`}
              displayType={displayType}
              className='counter'
              sx={{
                opacity: isDataReady && remainingCount > 0 ? 1 : 0,
                position: 'absolute',
                left: counterLeftPosition,
              }}
            />
          )}
        </>
      ) : (
        <Placeholder placeholder={placeholder} />
      )}
    </StyledSelectedOptions>
  );
};

const Placeholder = ({ placeholder }: { placeholder?: string }) => {
  if (!placeholder) {
    return null;
  }

  return <StyledPlaceholder variant='body-sm-regular'>{placeholder}</StyledPlaceholder>;
};
