import type { SxProps, Theme } from '@mui/material';

import { PuiIcon, PuiSvgIcon } from '../../../icons';
import type { DisplayType } from '../types';
import { getDropdownChipLabel } from '../utils';
import { StyledChip } from './StyledComponents';

interface PuiChipProps {
  value: string;
  displayType: DisplayType;
  chipSx?: SxProps<Theme>;
  isHidden?: boolean;
  isEditable?: boolean;
  onDelete?: (value: string) => void;
}

export const PuiChip: React.FC<PuiChipProps> = ({ value, displayType, chipSx, isHidden, isEditable, onDelete }) => (
  <StyledChip
    key={isHidden ? `hidden-${value}` : value}
    label={getDropdownChipLabel(value)}
    displayType={displayType}
    sx={{
      opacity: isHidden ? 0 : 1,
      ...chipSx,
    }}
    onDelete={isEditable ? () => onDelete?.(value) : undefined}
    deleteIcon={
      <PuiSvgIcon
        icon={PuiIcon.XSm}
        width={10}
        height={10}
      />
    }
  />
);
