import type { SingleSelectDisplayTypes, SingleSelectOption } from '../types';
import { SingleSelectDropdown } from './SingleSelectDropdown';

interface SingleSelectEditProps {
  value: string;
  displayType?: SingleSelectDisplayTypes;
  options: SingleSelectOption[];
  disabled?: boolean;
  open?: boolean;
  onChange: (value: string) => void;
  className?: string;
  onMenuOpen?: () => void;
  onMenuClose?: () => void;
  placeholder?: string;
}

export const SingleSelectEdit: React.FC<SingleSelectEditProps> = ({
  options = [],
  disabled,
  value,
  displayType,
  open = false,
  onChange,
  className,
  onMenuOpen,
  onMenuClose,
  placeholder,
}) => {
  return (
    <SingleSelectDropdown
      disabled={disabled}
      onValueChange={onChange}
      singleSelectOptions={options}
      selectedValue={value}
      displayType={displayType}
      open={open}
      className={className}
      onMenuOpen={onMenuOpen}
      onMenuClose={onMenuClose}
      placeholder={placeholder}
    />
  );
};
