import type { ThemeOptions } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material';
import type { ReactElement } from 'react';
import { useEffect, useState } from 'react';

import { deepMerge, defaultTheme, GlobalTheme, GlobalThemeActionType } from '../../ui';

export const GlobalThemeProvider: React.FC<{ children: ReactElement | ReactElement[] }> = ({ children }) => {
  const [theme, setTheme] = useState<ThemeOptions>(defaultTheme);

  useEffect(() => {
    const handleGetGlobalTheme = () => {
      GlobalTheme.dispatchEvent(GlobalThemeActionType.GET_GLOBAL_THEME_RESPONSE, theme);
    };

    const handleUpdateGlobalTheme = (e: CustomEvent<ThemeOptions>) => {
      const newTheme = deepMerge(theme, e.detail);
      setTheme(newTheme);
      GlobalTheme.dispatchEvent(GlobalThemeActionType.UPDATE_GLOBAL_THEME_RESPONSE, newTheme);
    };

    window.addEventListener(GlobalThemeActionType.GET_GLOBAL_THEME, handleGetGlobalTheme as EventListener);
    window.addEventListener(GlobalThemeActionType.UPDATE_GLOBAL_THEME, handleUpdateGlobalTheme as EventListener);

    return () => {
      window.removeEventListener(GlobalThemeActionType.GET_GLOBAL_THEME, handleGetGlobalTheme as EventListener);
      window.removeEventListener(GlobalThemeActionType.UPDATE_GLOBAL_THEME, handleUpdateGlobalTheme as EventListener);
    };
  }, [theme]);

  return <ThemeProvider theme={createTheme(theme)}>{children}</ThemeProvider>;
};
