import type { BoxProps, SliderProps } from '@mui/material';
import type { FC } from 'react';

import { PuiSpacer } from '../../../components';
import { useSliderParams } from '../hooks/SliderParams';
import { StyledSlider, Wrapper } from '../StyledComponents';
import type { SliderDisplayType } from '../types';

export interface BaseSliderProps extends SliderProps {
  displayType?: SliderDisplayType;
  mainColor?: string;
  percentage?: boolean;
  value: number | number[];
  wrapperWidth?: string;
  wrapperSx?: BoxProps['sx'];
  isViewMode?: boolean;
}

export const BaseSlider: FC<BaseSliderProps> = ({
  displayType = 'small',
  percentage = false,
  mainColor,
  value,
  disabled = false,
  wrapperWidth = '100%',
  wrapperSx,
  isViewMode = false,
  ...props
}) => {
  const { sliderParams, color } = useSliderParams(displayType, mainColor);

  return (
    <Wrapper
      sx={wrapperSx}
      style={{ width: wrapperWidth }}
    >
      <PuiSpacer width='16px' />
      <StyledSlider
        value={value}
        aria-labelledby='input-slider'
        valueLabelDisplay='auto'
        className={isViewMode ? 'view-mode' : undefined}
        valueLabelFormat={percentage ? (value: number) => `${value}%` : undefined}
        height={sliderParams.sliderLineHeight}
        background={sliderParams.railBackgroundColor}
        border={sliderParams.border}
        texture={color}
        disabled={disabled}
        {...props}
      />
    </Wrapper>
  );
};
