import type { ChangeEvent } from 'react';
import { useCallback, useState } from 'react';

import { StyledTextField } from '../../StyledComponents';
import { validateUrl } from '../common/helpers';
import type { PuiLinkEditProps } from '../types';

export const PuiLinkEdit: React.FC<PuiLinkEditProps> = props => {
  const {
    value,
    placeholder = 'Enter web address',
    onChange,
    setValidationError,
    showError = true,
    autoFocus = false,
  } = props;
  const [linkValue, setLinkValue] = useState<string>(value);
  const [error, setError] = useState<string | null>(null);

  const handleChange = useCallback(
    (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
      const inputValue = e.target.value;
      setLinkValue(inputValue);

      if (typeof onChange === 'function') {
        onChange(inputValue);
      }

      const isValidUrl = inputValue.length > 0 ? validateUrl(inputValue) : true;
      const errorMessage = isValidUrl ? null : 'Enter a valid URL address';

      setError(errorMessage);
      if (setValidationError) {
        setValidationError(errorMessage);
      }
    },
    [onChange, setValidationError]
  );

  const haveCustomPlaceholder = props.placeholder ? 'show-placeholder' : '';

  return (
    <StyledTextField
      size='small'
      value={linkValue}
      autoFocus={autoFocus}
      onChange={handleChange}
      placeholder={placeholder}
      helperText={showError && error}
      error={showError && !!error}
      className={haveCustomPlaceholder}
    />
  );
};
