import type { ChangeEvent, KeyboardEvent } from 'react';
import { useState } from 'react';

import { isLengthTooBig, processNumberForSave } from '../../utils';
import type { PuiNumberEditProps } from '../types';
import { PercentageBox, TextInput, TextInputContainer } from './StyledComponents';

export const PuiNumberEdit: React.FC<PuiNumberEditProps> = ({
  value,
  disabled = false,
  percentage = false,
  signed = true,
  decimalPlaces = 0,
  wrapperSx,
  inputSx,
  isLoading = false,
  onSave,
  onChange,
  placeholder = '',
  autoFocus = false,
}) => {
  const [number, setNumber] = useState<string>(value?.toString() ?? '');
  const hasOnChange = typeof onChange === 'function';

  const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const regEx = signed ? /^-?\d+(\.\d+)?$|^-?\d+\.$/ : /^\d+(\.\d+)?$|^\d+\.$/;
    const extraCheck = signed ? '-' : '';
    let curValue = e.currentTarget.value;

    if (curValue.indexOf(',') !== -1) {
      curValue = curValue.replace(',', '.');
    }
    if (isLoading || isLengthTooBig(curValue)) return;
    if (curValue && curValue !== extraCheck && !regEx.test(curValue)) return;

    setNumber(curValue);
    hasOnChange && onChange(processNumberForSave(curValue, decimalPlaces));
  };

  const onKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
    if (!e.shiftKey && e.key === 'Enter') {
      onSave(number === '' ? undefined : processNumberForSave(number, decimalPlaces));

      return;
    }

    const countToChange = e.shiftKey ? 10 : 1;

    if (e.key === 'Down' || e.key === 'ArrowDown') {
      setNumber(prevVal => {
        const newVal = prevVal ? String(+prevVal - countToChange) : '1';
        hasOnChange && onChange(+newVal);

        return newVal;
      });

      return;
    }
    if (e.key === 'Up' || e.key === 'ArrowUp') {
      setNumber(prevVal => {
        const newVal = prevVal ? String(+prevVal + countToChange) : '0';
        hasOnChange && onChange(+newVal);

        return newVal;
      });
    }
  };

  return (
    <TextInputContainer sx={wrapperSx}>
      <TextInput
        size='small'
        value={number}
        placeholder={placeholder}
        endAdornment={percentage ? <PercentageBox>%</PercentageBox> : undefined}
        sx={inputSx}
        maxRows={12}
        autoFocus={autoFocus}
        disabled={disabled}
        onChange={handleChange}
        onKeyDown={onKeyDown}
      />
    </TextInputContainer>
  );
};
