import { useCallback, useRef, useState } from 'react';

import { PuiGradients } from '../../../colors/PuiGradients';
import type { PuiContact } from '../..';
import { Colors, PuiFab, PuiIcon, PuiMenu, PuiSvgIcon, useCoverContainerResize, useTheme } from '../..';
import {
  ContactAvatar,
  ContactAvatarWrapper,
  ContactCardHeader,
  ContactHeaderButton,
  CoverButton,
  CoverImageWrapper,
  CoverMenuItem,
  FabWrapper,
} from './StyledComponents';
import type { ContactCardActions } from './types';

export interface PuiContactCardHeaderProps {
  contact: PuiContact;
  actions: ContactCardActions;
  coverImage?: { position: { y: number }; url: string; containerDimensions?: { width: number; height: number } };
  showCoverButton?: boolean;
}

export const PuiContactCardHeader: React.FC<PuiContactCardHeaderProps> = ({
  coverImage,
  contact,
  actions: { onBack, onEdit, onShare, onCoverSave, onCoverDelete, onClose },
  showCoverButton = false,
}) => {
  const theme = useTheme();
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const coverImageRef = useRef<HTMLImageElement | null>(null);
  const coverWrapperRef = useRef<HTMLDivElement | null>(null);
  const coverMenuOpen = Boolean(anchorEl);

  const { position } = useCoverContainerResize(
    coverWrapperRef,
    coverImageRef,
    coverImage?.containerDimensions ?? null,
    coverImage?.position || { y: 0 },
    coverImage?.url || '',
    { debounceTime: 100 }
  );

  const handleClick = useCallback((event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  }, []);

  const handleClose = useCallback(() => {
    setAnchorEl(null);
  }, []);

  const handleUpload = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };

  return (
    <ContactCardHeader>
      {onBack && (
        <ContactHeaderButton
          sx={{ left: '16px' }}
          onClick={onBack}
        >
          <PuiSvgIcon
            icon={PuiIcon.ArrowLeft}
            width={16}
            height={16}
          />
        </ContactHeaderButton>
      )}
      {onClose && (
        <ContactHeaderButton
          sx={{ right: '16px' }}
          onClick={onClose}
        >
          <PuiSvgIcon
            icon={PuiIcon.XClose}
            width={16}
            height={16}
          />
        </ContactHeaderButton>
      )}
      <CoverImageWrapper ref={coverWrapperRef}>
        {coverImage && (
          <img
            ref={coverImageRef}
            src={coverImage.url}
            alt='cover background'
            style={
              !Object.is(position.y, -0)
                ? {
                    transform: `translateY(${position.y}px)`,
                  }
                : { height: '100%' }
            }
          />
        )}
      </CoverImageWrapper>
      <ContactAvatarWrapper>
        <ContactAvatar
          size={80}
          contact={contact}
        />
        {showCoverButton && (
          <CoverButton
            id='cover-button'
            size='small'
            startIcon={
              <PuiSvgIcon
                icon={PuiIcon.Image}
                width={16}
                height={16}
              />
            }
            aria-haspopup='true'
            aria-expanded={coverMenuOpen ? 'true' : undefined}
            onClick={handleClick}
          >
            Cover
          </CoverButton>
        )}
        <PuiMenu
          id='basic-menu'
          anchorEl={anchorEl}
          open={coverMenuOpen}
          onClose={handleClose}
          MenuListProps={{
            'aria-labelledby': 'cover-button',
          }}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
        >
          <CoverMenuItem
            onClick={handleUpload}
            disabled={!onCoverSave}
          >
            <PuiSvgIcon
              icon={PuiIcon.Upload}
              width={16}
              height={16}
            />
            Upload photo
          </CoverMenuItem>
          <input
            ref={fileInputRef}
            id='upload'
            type='file'
            accept='.jpg,.jpeg'
            hidden
            onChange={e => onCoverSave?.(e.target.files ? e.target.files[0] : null)}
          />
          <CoverMenuItem
            divider
            disabled
          >
            <PuiSvgIcon
              icon={PuiIcon.Move}
              width={16}
              height={16}
            />
            Move
          </CoverMenuItem>
          <CoverMenuItem
            onClick={onCoverDelete}
            disabled={!onCoverDelete}
          >
            <PuiSvgIcon
              icon={PuiIcon.Trash}
              width={16}
              height={16}
            />
            Delete
          </CoverMenuItem>
        </PuiMenu>
      </ContactAvatarWrapper>
      <FabWrapper>
        {onEdit && (
          <PuiFab
            size='small'
            onClick={onEdit}
            aria-label='edit'
            background={theme.palette.common.white}
            sx={{ '&:hover .MuiSvgIcon-root': { stroke: theme.palette.grey[600] } }}
          >
            <PuiSvgIcon
              icon={PuiIcon.Edit2}
              width={16}
              height={16}
              stroke={theme.palette.grey[400]}
            />
          </PuiFab>
        )}
        {onShare && (
          <PuiFab
            size='small'
            onClick={onShare}
            aria-label='share'
            sx={{
              backgroundColor: Colors.blue.base,
              '&:hover': {
                backgroundImage: PuiGradients[0],
              },
            }}
          >
            <PuiSvgIcon
              icon={PuiIcon.Share}
              width={16}
              height={16}
            />
          </PuiFab>
        )}
      </FabWrapper>
    </ContactCardHeader>
  );
};
