import type { BoxProps } from '@mui/material';

import { Globe2, SocialMediaDomain } from '../../..';
import {
  SocialIconsBehance,
  SocialIconsDribbble,
  SocialIconsFacebook,
  SocialIconsGitHub,
  SocialIconsGoogle,
  SocialIconsInstagram,
  SocialIconsLinkedIn,
  SocialIconsMedium,
  SocialIconsPinterest,
  SocialIconsTeams,
  SocialIconsTelegram,
  SocialIconsTikTok,
  SocialIconsTumblr,
  SocialIconsViber,
  SocialIconsVimeo,
  SocialIconsWhatsUp,
  SocialIconsX,
  SocialIconsYouTube,
} from '../..';
import { SocialIconWrapper } from './StyledComponents';

interface SocialMediaIconProps {
  domain: SocialMediaDomain | null;
  sx?: BoxProps['sx'];
}

export const PuiSocialMediaIcon: React.FC<SocialMediaIconProps> = ({ domain, sx }) => {
  const socialMediaIcons: Record<SocialMediaDomain, JSX.Element | null> = {
    [SocialMediaDomain.Facebook]: <SocialIconsFacebook />,
    [SocialMediaDomain.Telegram]: <SocialIconsTelegram />,
    [SocialMediaDomain.WhatsApp]: <SocialIconsWhatsUp />,
    [SocialMediaDomain.LinkedIn]: <SocialIconsLinkedIn />,
    [SocialMediaDomain.Instagram]: <SocialIconsInstagram />,
    [SocialMediaDomain.X]: <SocialIconsX />,
    [SocialMediaDomain.Google]: <SocialIconsGoogle />,
    [SocialMediaDomain.YouTube]: <SocialIconsYouTube />,
    [SocialMediaDomain.Vimeo]: <SocialIconsVimeo />,
    [SocialMediaDomain.GitHub]: <SocialIconsGitHub />,
    [SocialMediaDomain.Medium]: <SocialIconsMedium />,
    [SocialMediaDomain.Pinterest]: <SocialIconsPinterest />,
    [SocialMediaDomain.Dribbble]: <SocialIconsDribbble />,
    [SocialMediaDomain.Behance]: <SocialIconsBehance />,
    [SocialMediaDomain.TikTok]: <SocialIconsTikTok />,
    [SocialMediaDomain.Tumblr]: <SocialIconsTumblr />,
    [SocialMediaDomain.MicrosoftTeams]: <SocialIconsTeams />,
    [SocialMediaDomain.Viber]: <SocialIconsViber />,
  };
  const getSocialMediaIcon = domain ? (
    socialMediaIcons[domain]
  ) : (
    <Globe2
      width={28}
      height={28}
    />
  );

  return <SocialIconWrapper sx={sx}>{getSocialMediaIcon}</SocialIconWrapper>;
};
