import { useCallback, useState } from 'react';

import { useTheme } from '../../../hooks';
import { PuiIcon } from '../../../icons';
import type { PuiSingleEmailProps } from '../types';
import { validateEmail } from '../validateEmail';
import { EmailInput } from './EmailInput';
import { EmailPopupMenu } from './EmailPopupMenu';
import { SingleEmailWrapper, StartSvgIcon } from './StyledComponents';

export const PuiSingleEmail: React.FC<PuiSingleEmailProps> = ({
  value,
  multiple,
  anchorEl,
  disabled,
  wrapperSx,
  inputSx,
  popupMenuSx,
  onChange,
  onSave,
  autoFocus,
  placeholder,
}) => {
  const theme = useTheme();

  const [text, setText] = useState<string>(value);
  const [isOpened, setIsOpened] = useState<boolean>(false);

  const handleSave = () => {
    const isValid = validateEmail(text);
    const validationError = text && !isValid ? 'Enter a valid email address' : null;
    const value = text !== '' ? [text] : null;

    typeof onSave === 'function' && onSave(value, { validationError: validationError });
  };

  const handleSaveNewEmail = useCallback(
    (newValue: string) => {
      onSave([value, newValue]);
      setIsOpened(false);
    },
    [onSave, value]
  );

  const handleChange = useCallback(
    (newValue: string) => {
      setText(newValue);
      const isValid = validateEmail(newValue);
      const validationError = newValue && !isValid ? 'Enter a valid email address' : null;
      const value = newValue !== '' ? [newValue] : null;

      typeof onChange === 'function' && onChange(value, { validationError });
    },
    [onChange]
  );

  const handleToggleMenu = useCallback(() => setIsOpened(prevState => !prevState), []);

  return (
    <SingleEmailWrapper sx={wrapperSx}>
      <EmailInput
        value={text}
        validationError={null}
        disabled={disabled}
        sx={inputSx}
        onChange={handleChange}
        styleMod='borderless'
        onKeyDown={handleSave}
        InputProps={{
          startAdornment:
            multiple && value && !disabled ? (
              <StartSvgIcon
                icon={PuiIcon.PlusCircle}
                width={20}
                height={20}
                stroke={theme.palette.primary.main}
                aria-label='add'
                onClick={handleToggleMenu}
              />
            ) : undefined,
        }}
        placeholder={placeholder}
        autoFocus={autoFocus}
      />
      <EmailPopupMenu
        isOpened={isOpened}
        initialValue=''
        anchorEl={anchorEl}
        popupMenuSx={popupMenuSx}
        onCloseMenu={handleToggleMenu}
        onSave={handleSaveNewEmail}
      />
    </SingleEmailWrapper>
  );
};
