import { useCallback, useRef, useState } from 'react';

import { PuiIcon, useTheme } from '../../../';
import type { MultiplePhonesProps, PhoneValueType } from '../types';
import { PhoneInput } from './PhoneInput';
import { PhonePopupMenu } from './PhonePopupMenu';
import { EndSvgIcon, MultiplePhoneWrapper, PhoneItemWrapper, StartMultipleSvgIcon } from './StyledComponents';

export const MultiplePhones: React.FC<MultiplePhonesProps> = ({
  values,
  disabled,
  defaultCountry,
  inputSx,
  popupSx,
  multiplePhoneWrapperSx,
  phoneItemWrapperSx,
  onSave,
  autoFocus,
  placeholder,
  className,
}) => {
  const theme = useTheme();

  const [editMultiplePhone, setEditMultiplePhone] = useState('');
  const [editMultipleCountryCode, setEditMultipleCountryCode] = useState('');
  const [anchorEl, setAnchorEl] = useState(null);
  const anchorRef = useRef(null);

  const isOpened = Boolean(anchorEl);
  const handleSave = (newValue: PhoneValueType) => {
    const changedPhoneIndex = values.findIndex(item => item.phone === editMultiplePhone);
    const newPhones = [...values];

    if (changedPhoneIndex === -1) {
      newPhones.push(newValue);
    } else {
      newPhones[changedPhoneIndex] = newValue;
    }

    onSave(newPhones);
  };

  const handleCloseMenu = () => {
    setEditMultiplePhone('');
    setEditMultipleCountryCode('');
    setAnchorEl(null);
  };

  const handleOpenMenu = (editingPhone: PhoneValueType) => {
    if (disabled) return;
    setEditMultiplePhone(editingPhone.phone);
    setEditMultipleCountryCode(editingPhone.countryCode);
    setAnchorEl(anchorRef.current);
  };

  const handleRemovePhone = (itemIndex: number) => {
    if (disabled) return;
    const newPhones = values.slice(0, itemIndex).concat(values.slice(itemIndex + 1));

    onSave(newPhones);
  };

  const emptyFunc = useCallback(() => {
    // empty function
  }, []);

  return (
    <MultiplePhoneWrapper
      ref={anchorRef}
      sx={multiplePhoneWrapperSx}
    >
      {values.map((item: PhoneValueType, index: number) => (
        <PhoneItemWrapper
          key={`${item.phone}-${index}`}
          sx={phoneItemWrapperSx}
        >
          {index === 0 && !disabled && (
            <StartMultipleSvgIcon
              icon={PuiIcon.PlusCircle}
              width={20}
              height={20}
              stroke={theme.palette.primary.main}
              aria-label='add'
              onClick={() => handleOpenMenu({ phone: '', countryCode: '' })}
            />
          )}
          <PhoneInput
            value={item.phone}
            defaultCountry={item.countryCode || defaultCountry}
            styleMod='static'
            validationError={null}
            isReadOnly
            sx={inputSx}
            onChange={emptyFunc}
            autoFocus={autoFocus}
            placeholder={placeholder}
            className={className}
          />
          {!disabled && (
            <>
              <EndSvgIcon
                icon={PuiIcon.Pencil}
                width={12}
                height={12}
                stroke={theme.palette.grey[300]}
                aria-label='edit'
                onClick={() => handleOpenMenu(item)}
              />
              <EndSvgIcon
                icon={PuiIcon.XClose}
                width={16}
                height={16}
                stroke={theme.palette.grey[300]}
                aria-label='delete'
                onClick={() => handleRemovePhone(index)}
              />
            </>
          )}
        </PhoneItemWrapper>
      ))}
      <PhonePopupMenu
        isOpened={isOpened}
        initialValue={editMultiplePhone}
        initialCountryCode={!editMultiplePhone ? defaultCountry : editMultipleCountryCode}
        isEditing={!!editMultiplePhone}
        anchorEl={anchorEl}
        popupSx={popupSx}
        inputSx={inputSx}
        onCloseMenu={handleCloseMenu}
        onSave={handleSave}
      />
    </MultiplePhoneWrapper>
  );
};
