import { ComponentMode } from '../../types';
import { PuiNumberEdit } from './edit/NumberEdit';
import type { PuiNumberProps } from './types';
import { PuiNumberView } from './view/NumberView';

export const PuiNumber: React.FC<PuiNumberProps> = ({
  value,
  format,
  decimalPlaces = 0,
  percentage = false,
  mode = ComponentMode.Edit,
  ...props
}) => {
  if (mode === ComponentMode.Edit) {
    return (
      <PuiNumberEdit
        value={value}
        percentage={percentage}
        decimalPlaces={decimalPlaces}
        {...props}
      />
    );
  }

  if (mode === ComponentMode.View) {
    return (
      <PuiNumberView
        value={value}
        format={format}
        percentage={percentage}
        decimalPlaces={decimalPlaces}
      />
    );
  }

  return null;
};
