import type { BoxProps, ChipProps } from '@mui/material';
import { useCallback } from 'react';

import { BaseSelectedOptions } from '../common/BaseSelectedOptions';
import { PuiChip } from '../edit/Chip';
import { RemainingCountChip } from '../edit/StyledComponents';
import type { DisplayType, RenderChipsProps } from '../types';

interface SelectedOptionsViewProps {
  selectedValues: string[];
  displayType: DisplayType;
  showCircleButton: boolean;
  currentColumnWidth: number;
  optionSx?: BoxProps['sx'];
  chipSx?: ChipProps['sx'];
  remainingCount: number;
  placeholder?: string;
  setRemainingCount: (remainingCount: number) => void;
  hideTransparentItems?: boolean;
}

export const SelectedOptionsView: React.FC<SelectedOptionsViewProps> = props => {
  const renderChips = useCallback(
    ({ visibleChips, hiddenChips, isReady }: RenderChipsProps) => (
      <>
        {visibleChips.map(value => (
          <PuiChip
            key={value}
            value={value}
            displayType={props.displayType}
            chipSx={{ opacity: isReady ? 1 : 0, ...props.chipSx }}
          />
        ))}
        {!props.hideTransparentItems && (
          <RemainingCountChip
            key='remaining-counter'
            label={`+${props.remainingCount}`}
            displayType={props.displayType}
            className='counter'
            sx={{ opacity: props.remainingCount > 0 ? 1 : 0 }}
          />
        )}
        {hiddenChips.map(value => (
          <PuiChip
            key={`hidden-${value}`}
            value={value}
            displayType={props.displayType}
            chipSx={props.chipSx}
            isHidden
          />
        ))}
      </>
    ),
    [props.displayType, props.chipSx, props.remainingCount, props.hideTransparentItems]
  );

  return (
    <BaseSelectedOptions
      {...props}
      renderChips={renderChips}
    />
  );
};
