import { ComponentMode } from '../../types';
import { PuiStatusEdit } from './edit/StatusEdit';
import type { PuiStatusProps } from './types';
import { PuiStatusView } from './view/StatusView';

export const PuiStatus: React.FC<PuiStatusProps> = ({
  options = [],
  disabled = false,
  value,
  displayType,
  handleChanges,
  mode = ComponentMode.Edit,
  className,
  onMenuOpen,
  onMenuClose,
  open,
  placeholder,
  sx,
  onHoverViewActions = false,
  manualCellEdit,
  onClearValue,
}) => {
  if (mode === ComponentMode.View) {
    return (
      <PuiStatusView
        isLocked={disabled}
        statusOptions={options}
        selectedValue={value}
        displayType={displayType}
        onClearValue={onClearValue}
        manualCellEdit={manualCellEdit}
        onHoverViewActions={onHoverViewActions}
        sx={sx}
      />
    );
  }
  if (mode === ComponentMode.Edit) {
    return (
      <PuiStatusEdit
        disabled={disabled}
        handleChanges={handleChanges}
        options={options}
        value={value}
        displayType={displayType}
        className={className}
        onMenuOpen={onMenuOpen}
        onMenuClose={onMenuClose}
        open={open}
        placeholder={placeholder}
        sx={sx}
      />
    );
  }

  return null;
};
