import type { AutocompleteProps as PuiAutocompleteProps, SelectProps } from '@mui/material';
import PuiAutocomplete from '@mui/material/Autocomplete';
import PuiNativeSelect from '@mui/material/NativeSelect';
import type {
  BaseSelectProps,
  SelectChangeEvent as PuiSelectChangeEvent,
  SelectProps as PuiSelectProps,
} from '@mui/material/Select';
import PuiSelect from '@mui/material/Select';

import { PuiIcon, PuiSvgIcon, useTheme } from '..';

const IconComponent: BaseSelectProps['IconComponent'] = props => {
  const theme = useTheme();

  return (
    <PuiSvgIcon
      icon={PuiIcon.ChevronDown}
      stroke={theme.palette.grey[400]}
      sx={{
        height: 16,
        width: 16,
        '&.MuiSvgIcon-root': {
          right: 16,
          top: 'auto',
        },
      }}
      {...props}
    />
  );
};

const PuiSelectV2 = <Value,>(props: SelectProps<Value>) => {
  const { sx: sxOverrides, MenuProps: menuPropsOverrides, ...rest } = props;
  const theme = useTheme();

  const sx = {
    minHeight: '40px',
    backgroundColor: theme.palette.background.default,
    '& .MuiSelect-select.MuiInputBase-input': {
      padding: '0px 15px',
      display: 'flex',
      alignSelf: 'stretch',
      alignItems: 'center',
    },
    '&.MuiInputBase-root:not(.Mui-error)': {
      fieldset: {
        borderColor: theme.palette.grey[50],
      },
      '&:hover': {
        fieldset: {
          borderColor: theme.palette.grey[100],
        },
      },
    },
    '&.MuiInputBase-root.Mui-focused': {
      fieldset: {
        borderWidth: '1px',
      },
      '&:hover': {
        fieldset: {
          borderWidth: '1px',
        },
      },
      background: theme.palette.background.paper,
      '&:not(.Mui-error)': {
        fieldset: {
          borderColor: theme.palette.primary.main,
        },
        '&:hover': {
          fieldset: {
            borderColor: theme.palette.primary.main,
          },
        },
      },
    },
    ...sxOverrides,
  };

  const menuListPropsOverrides = menuPropsOverrides?.MenuListProps;
  if (menuListPropsOverrides) {
    delete menuPropsOverrides.MenuListProps;
  }

  const menuProps = {
    sx: {
      '& .MuiBackdrop-root': {
        backgroundColor: 'transparent',
      },
      '& .MuiPaper-root': {
        marginTop: '4px',
        marginBottom: '4px',
      },
    },
    MenuListProps: {
      sx: {
        paddingTop: '4px',
        paddingBottom: '4px',
        maxHeight: '300px',
      },
      ...menuListPropsOverrides,
    },
    ...menuPropsOverrides,
  };

  return (
    <PuiSelect
      sx={sx}
      IconComponent={IconComponent}
      MenuProps={menuProps}
      {...rest}
    />
  );
};

export { PuiAutocomplete, PuiNativeSelect, PuiSelect, PuiSelectChangeEvent, PuiSelectV2 };
export type { PuiAutocompleteProps, PuiSelectProps };
