import React from 'react';

import type { StatusItemProps } from '../types';
import { LeftIndicator, LeftIndicatorOptionContainer, StyledLeftIndicatorContainer } from './StyledComponents';

export const LeftIndicatorStatusItem: React.FC<StatusItemProps> = ({ option, viewMode = false }) => {
  return (
    <LeftIndicatorOptionContainer className={viewMode ? 'view' : ''}>
      <LeftIndicator
        color={option.color}
        className={viewMode ? 'view' : ''}
      />
      <StyledLeftIndicatorContainer>{option.label}</StyledLeftIndicatorContainer>
    </LeftIndicatorOptionContainer>
  );
};
